import MessageForwarder from 'MessageForwarder';
import MessageHandler from 'MessageHandler';

export default class ChromeApp {
  addProfilingUtils() {
    window.profileBegin = function(tag, ...args) {
      Array.prototype.unshift.call(args, "<A-"+tag+"> ");
      console.log.apply(console, args);
    }
    window.profileEnd = function(tag, ...args) {
      Array.prototype.unshift.call(args, "<B-"+tag+"> ");
      console.log.apply(console, args);
    }
  }

  constructor($logger, $window) {
    this.addProfilingUtils();
    this.webgl = null;

    // Dependencies
    this.$logger = $logger;
    this.$window = $window;

    this.listeners = {};
  }

  initialize() {
    return new Promise((resolve) => {
      this.$logger.debug('[CoreChromeApp] Initialize');
      var loadCompleted = () => {
        this.$window.addEventListener('message', (event) => {
          this.handleMessage.call(this, event);
        });
        this.webgl = this.attachListeners('webgl', (view) => {
          this.sendHelloMessage(view);
          MessageHandler.setWebGL(view);
          resolve();
        });
      };
      // Have the document already loaded?
      if ( this.$window.document.readyState === 'complete') {
        loadCompleted();
      } else { // If not => listen for load event
        this.$window.addEventListener('load', loadCompleted);
      }
    });
  }

  register(listenerName, listenerInstance) {
    this.listeners[listenerName] = listenerInstance;

    // Setup listener in webgl context
    this.webgl.contentWindow.postMessage({
      type: 'register',
      name: listenerName,
      methods: listenerInstance.getPublicFunctions(),
    }, '*');
  }

  remove(listenerName) {
    delete this.listeners[listenerName];
  }

  sendHelloMessage(webview) {
    webview.contentWindow.postMessage({
      sender: 'hello',
    }, '*');
    this.$logger.debug(`Sent hello message to ${webview.id}`);
  }

  attachListeners(id, afterLoadCallback) {
    const view = document.getElementById(id);

    view.addEventListener('dialog', (event) => {
      this.$logger.debug(`[${id}] dialog of type ${event.messageType} : ${event.messageText}`);
      event.dialog.ok('');
    });

    view.addEventListener('consolemessage', (event) => {
      this.$logger.debug(`[${id}] ${event.message}`);
    });

    view.addEventListener('contentload', () => {
      this.$logger.debug(`Finished loading ${id}`);
      if (afterLoadCallback) {
        afterLoadCallback(view);
      }
    });

    view.addEventListener('message', (event) => {
      this.$logger.debug(`[${id}] - EVENT[message] - `, event);
    });

    return view;
  }

  handleMessage($event) {
    // this.$logger.log('ChromeApp - handleMessage - Received message:', $event);

    this.$window.listeners = this.listeners;

    try {
      this.listeners[$event.data.target].handle($event.data);
    } catch (error) {
      this.$logger.error('ChromeApp - handleMessage - No listener registered!', error);
    }
  }
}



