import BaseModule from 'components/BaseModule';
import MessageHandler from 'MessageHandler';

export default class Storage extends BaseModule{

  constructor() {
    super();
    this.currentFileEntry = null;
    this.savingFile = false;
  }
  
  isSavingFile = ()  => {
    return this.savingFile;
  }

  showFilePicker = (mimeType, filePath) => {
    const fileTypes = [{
      mimeTypes: [mimeType],
    }];
    chrome.fileSystem.chooseEntry({ type: 'openFile', accepts: fileTypes }, (entry) => {
      if (!entry) {
        return;
      }

      if (chrome.runtime.lastError) {
        console.warn('showFilePicker - chooseEntry error: ', chrome.runtime.lastError.message);
        return;
      }


      this.currentFileEntry = entry;
      
      const fileSplit = entry.name.split('.');
      const fileExt = fileSplit[fileSplit.length-1];
      
      this.currentFileEntry.file( (file) => {
        const saveFilePath = `${filePath}.${fileExt}`;
        const dataURL = `uniwebview://FileSelectedFromPicker?FilePath=${saveFilePath}&MimeType=${mimeType}`;
        this.saveFileToIndexDB(saveFilePath, file, dataURL);
      });
    });
  }

  saveFile = (fileBlob, extension, writeSuccessCallback, writeErrorCallback) => {
    this.savingFile = true;
    const config = { type: 'saveFile', suggestedName: `untitled.${extension}`, accepts: [ { extensions: [ `${extension}` ] } ] };
    chrome.fileSystem.chooseEntry(config, (writableEntry) => {
      if (chrome.runtime.lastError) {
        console.warn('saveFile - chooseEntry error: ', chrome.runtime.lastError.message);
        writeErrorCallback(chrome.runtime.lastError.message);
        this.savingFile = false;
        return;
      }

      if (!writableEntry) {
        writeErrorCallback(chrome.runtime.lastError.message);
        this.savingFile = false;
        return;
      }
      writableEntry.createWriter((writer) => {
        writer.onerror = (error) => {
          writeErrorCallback(error);
          this.savingFile = false;
        };
        writer.onwriteend = (e) => {
          if (e.loaded > 0 && e.total > 0 && e.loaded === e.total)
          {
            writeSuccessCallback();
            this.savingFile = false;
          }
        };

        writer.truncate(fileBlob.size);
        this.waitForIO(writer, () => {
          writer.seek(0);
          writer.write(fileBlob);
        });
      }, (error) => {
          writeErrorCallback(error);
          this.savingFile = false;
      });
    });
  }

  waitForIO(writer, callback) {
    // set a watchdog to avoid eventual locking:
    const start = Date.now();
    // wait for a few seconds
    const reentrant = () => {
      if (writer.readyState === writer.WRITING && Date.now() - start < 4000) {
        setTimeout(reentrant, 100);
        return;
      }
      if (writer.readyState === writer.WRITING) {
        // console.error( "Write operation taking too long, aborting!
        // (current writer readyState is " + writer.readyState + ")");
        writer.abort();
      }
      else {
        callback();
      }
    };
    setTimeout(reentrant, 100);
  }

  saveFileToIndexDB = (filePath, fileBlob, dataURL) => {
    MessageHandler.callWebGL('emscriptenFSHandler', 'saveFile', [{filePath, fileBlob, dataURL}]);
  }
}



