import BaseModule from 'components/BaseModule';
import MessageHandler from 'MessageHandler';

export default class Splashscreen extends BaseModule {

  constructor(options) {
    super();

    options = options || {};
    this.splashscreenTagId = options.splashscreenTagId || 'splashscreen';
    this.loadingBarId = options.loadingBarId || 'loadingBar';
    this.imageWidth = options.imageWidth || 2048;
    // Initially 5% to show a little progress from the start. If changed, remember to update CSS
    this.currentProgress = options.initialProgress || 0.05;

    chrome.app.window.current().onBoundsChanged.addListener(this.onBoundsChanged.bind(this));
        
    this.onBoundsChanged();

    this.unityProgress = 0;
    this.syncProgress = 0;
    this.unityDone = false;
  }

  mainElement = () => {
    if ( !this.splashscreenElement ){
      this.splashscreenElement = document.getElementById(this.splashscreenTagId);
    }
    return this.splashscreenElement;
  };

  barElement = () => {
    if ( !this.loadingBar ) {
      this.loadingBar = document.getElementById(this.loadingBarId);
    }
    return this.loadingBar;
  };

  hide = () => {
    console.debug('[CoreSplashScreen] Invoking hide()');
    this.mainElement().style.display = 'none';
  };

  show = () => {
    console.debug('[CoreSplashScreen] Invoking show()');
    const style = this.mainElement().style || {};
    if (style.removeProperty) {
        style.removeProperty('display');
    } else {
      style.display = '';
    }
    this.mainElement().style = style;
  };
  
  unityDoneLoading = () => {
    console.debug('[CoreSplashScreen] Unity done message');
    this.unityProgress = 1;
    this.unityDone = true;
    this.updateTotalProgress();
  };

  // Progress from unity
  updateProgress = (progress) => {
    console.debug('[CoreSplashScreen] Updating unity progress', progress);
    this.unityProgress = progress;
    this.updateTotalProgress();
  };

  updateSyncProgress = (progress) => {
    console.debug('[CoreSplashScreen] Updating sync progress', progress);
    this.syncProgress = progress;
    this.updateTotalProgress();
  };

  updateTotalProgress = () => {
    var totalProgress = (this.unityProgress + this.syncProgress) / 2;

    this.currentProgress = Math.max(this.currentProgress, totalProgress);
    console.log('[CoreSplashScreen] Updating total progress', this.currentProgress);
    //The full width from CSS minus margin
    var fullWidth = this.currentProgress * 100;
    
    if( this.barElement() ) {
      this.loadingBar.style.width = fullWidth + '%'; 
    }

    if ( this.syncProgress === 1 && this.unityDone ) {
      // Inform unity that the loading is done and it can show lobby
      console.log('[CoreSplashScreen] Both sync and unity is ready');
      MessageHandler.callUnity(null, 'JSFinishedLoading');
    }
  };
  
  onBoundsChanged = () => {

  };

}



