import MessageHandler from 'MessageHandler';

export default class WindowController {

    initialize($storage) {
        this.storage = $storage;

        chrome.app.window.current().onBoundsChanged.addListener(this.onBoundsChanged.bind(this));

        document.addEventListener('visibilitychange', () => {
            // Page Visibility Level 2 - https://www.w3.org/TR/page-visibility/
            if (document.visibilityState !== 'visible') {
                console.debug('pause - Document is hidden - invoking pause. document.visibilityState: ' + document.visibilityState);
                if (!this.storage.isSavingFile()) {
                    this.pause();
                }
            } else {
                console.debug('pause - Document is visible - invoking unpause. document.visibilityState: ' + document.visibilityState);
                if (!this.storage.isSavingFile()) {
                    this.unpause();
                }
            }
        });

        this.onBoundsChanged();
    }

    onBoundsChanged() {
        this.webviewElement = document.getElementById('webgl'); //webview element from chromecore
        this.parentDiv = document.getElementById('parent'); //parent div from chromecore

        // let width = chrome.app.window.current().innerBounds.width;
        // let height = chrome.app.window.current().innerBounds.height;
        let width = Math.min(chrome.app.window.current().outerBounds.width, chrome.app.window.current().innerBounds.width);
        let height = Math.min(chrome.app.window.current().outerBounds.height, chrome.app.window.current().innerBounds.height);



        // this.webviewElement.style.width = `${width}px`;
        // this.webviewElement.style.height = `${height}px`;
        //
        // this.parentDiv.style.width = `${width}px`;
        // this.parentDiv.style.height = `${height}px`;

        console.log('onBoundsChanged: setting width: ' + width + 'px, height: ' + height + 'px - innerBounds: [ width: ' + chrome.app.window.current().innerBounds.width +', height: ' + chrome.app.window.current().innerBounds.height + '] - outerBounds: [ width: ' + chrome.app.window.current().outerBounds.width + ', height: ' + chrome.app.window.current().outerBounds.height + '] - document: [ width: ' + document.body.clientWidth + ', height: ' + document.body.clientHeight + '] ');
        if (this.webviewElement && this.webviewElement.style && this.webviewElement.style.width) {
            console.log('webviewElement style: [width: ' + this.webviewElement.style.width + ', height: ' + this.webviewElement.style.height + ']');
        }
        MessageHandler.callWebGL('window', 'resize', [{width, height}]);
    }

    pause() {
        const dataURL = 'uniwebview://PauseUnpause?Pause=true';
        MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL);
    }

    unpause() {
        const dataURL = 'uniwebview://PauseUnpause?Pause=false';
        MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL);
    }
}



