import BaseModule from 'components/BaseModule';

export default class Webview extends BaseModule {

  constructor(settings, autosetup = true) {
    super();

    this.webview = null;
    this.eventHandlers = {};

    if (autosetup) {
      this.setup(settings);
    }
  }

  setup(settings) {
    this.createWebview();
    this.configureWebview(this.webview, settings);
    this.append();

    if (this.eventHandlers['contentload']) {
      this.webview.addEventListener('contentload', this.eventHandlers['contentload']);
    }
  }

  createWebview() {
    this.webview = document.createElement('webview');
  }

  configureWebview(parentObject, settings) {
    const parent = parentObject;
    for (const setting in settings) {
      if (settings.hasOwnProperty(setting)) {
        if (typeof settings[setting] === 'object') {
          this.configureWebview(parent[setting], settings[setting]);
        } else {
          parent[setting] = settings[setting];
        }
      }
    }
  }

  append() {
    document.body.appendChild(this.webview);
  }

  sendMessage(data) {
    this.webview.contentWindow.postMessage(data, '*');
  }

  // PUBLIC FUNCTIONS

  load = (url) => {
    this.webview.src = url;
  }

  evaluatingJavaScript = (js) => {
    this.sendMessage({
      code: js,
    });
  }

}



