import BaseModule from 'components/BaseModule';

/**
 * Component which allows for only running a method if on an approved OS or in development mode
 *
 * To set development mode add the following line to the manifest:
 *
 * "developmentMode" : true,
 *
 * NOTE: This will produce a warning about "Unrecognized manifest key 'developmentMode'."
 */
export default class OSLimiter extends BaseModule {

	/**
	 * Constructors the OSLimiter component
	 * @param  {Array}   approvedList         The list of OS' that should be approved. Default []
	 * @param  {Boolean} allowDevelopmentMode If limiter should look for development mode or not. Default true
	 * @param  {String}  parentElementId      Optional parameter specifying the ID of the parent element to 
	 *                                        append error message to. If not set, will append to body
	 */
	constructor(approvedList = [], allowDevelopmentMode = true, parentElementId ) {
		super();

		this.approvedList = approvedList;
		this.allowDevelopmentMode = allowDevelopmentMode;
		this.parentElementId = parentElementId;
	}

  // PUBLIC API
  runOnApprovedOS = ( approved ) => {
  	var service = this;
  	var approvedFn = approved;

    chrome.runtime.getPlatformInfo(function(info) {
    	if ( service.isSupportedOS( info.os ) || service.inDevelopmentMode() ) {
    		if ( approvedFn && typeof approvedFn === 'function' ) {
    			approvedFn();
    		}
    	} else {
    		console.log('Unsupported OS and not configured for development mode');
    
		    // Add unsupported class for html element
		    document.documentElement.className = 'unsupported';

		    var unsupportedOS = document.createElement('div');
		    unsupportedOS.id = 'unsupported';
		    // Set text from i18n
		    unsupportedOS.appendChild(document.createTextNode(chrome.i18n.getMessage('unsupportedOSMessage')));

		    var closeLink = document.createElement('a');
		    closeLink.appendChild(document.createTextNode(chrome.i18n.getMessage('closeApp')));
		    closeLink.className = 'close-button';
		    closeLink.onclick = function() {
		        window.close();
		    };

		    unsupportedOS.appendChild(closeLink);

		    // Append unsupported message to parent
		    if ( service.parentElementId ) {
		    	document.getElementById(service.parentElementId).appendChild(unsupportedOS);
		    } else {
		    	document.body.appendChild(unsupportedOS);
		    }
    	}
    });
  }

  // END PUBLIC API

  /**
	 * Check if app is in development mode. 
	 * 
	 * @return {boolean} true if in development mode, false otherwise
	 */
	inDevelopmentMode = () => {
	    return this.allowDevelopmentMode && !!chrome.runtime.getManifest().developmentMode;
	}

	isSupportedOS = ( os ) => {
	    return this.approvedList.indexOf( os ) !== -1;
	}
}


