import BaseModule from 'components/BaseModule';
import MessageHandler from 'MessageHandler';

export default class Killswitch extends BaseModule {

  constructor() {
    super();
  }

  request = (url) =>
  {
    var request = new XMLHttpRequest();
    request.onreadystatechange = function()
    {
        if (request.readyState == 4) //Response recieved and ready
        {
            let dataURL = 'uniwebview://KillswitchResponse';
            if(request.status == 200) //ok
            {
                dataURL += `?ReponseError=undefined`;
                dataURL += `&Data=${encodeURIComponent(request.responseText)}`;
                dataURL += `&ResponseHeader=${encodeURIComponent(request.getAllResponseHeaders())}`;
                MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL);
            }
            else //404 not found
            {
              dataURL += `?ReponseError=Error`;
              dataURL += `&Data=Error`;
              dataURL += `&ResponseHeader=None}`;
              MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL);
            }
        }
    };
    request.error = function() {

    };

    request.open("GET", url, true);
    request.send();
  }
}



