// NOTE(Catalin): API is still very fluid, things may change.

import BaseModule from 'components/BaseModule';
import MessageHandler from 'MessageHandler';
import GoogleDrive from 'components/GoogleDrive';
import SyncError from 'components/SyncError';

// TODO: adapt for offline scenarios
// TODO: refactor, remove unused code

/**
 * The RESUMABLE_UPLOAD_SIZE_THRESHOLD value controls which strategy is used for
 * uploading the file to Google Drive.
 *
 * If the file size is less than the threshold a multipart upload with the entire file uploaded in one request.
 * Google Drive documentation (as of September 2017) specifies that multipart upload
 * should not be done for files larger than 5MB.
 *
 * If the file size is larger than or equal to the threshold we will upload to Google Drive
 * using the resumable upload method, and upload the file in multiple chunks.
 *
 * @type {number}
 */
window.GD_RESUMABLE_UPLOAD_SIZE_THRESHOLD = 0.5 * 1024 * 1024;

export default class SyncFS extends BaseModule {

    constructor($logger, $splashscreen) {
        super();
        this.$logger = $logger;
        this.$splashscreen = $splashscreen;
        this._setup();
    }

    // SyncFS can be in various states during the app lifetime. Note that states annotated with a +
    // in this list will have additional information stored in a `reasons` field. Error states will
    // `reasons` will only exit when all reasons are resolved.
    // SyncFS status format: {state:<string>, reasons:[<string>, <string>...]}

    // States:
    // 'uninitialized'      // Initialization has yet to happen, unity can do nothing with us.
    // 'synced'             // Default state when nothing is happening, and anything can happen
    // 'synchronizing'      // Sync work in progress, show spinner.
    // +'offline'           // Something went wrong during upstream sync. The app will recover, but
                            // users should know their data is not being synced.
    // 'doing_big_sync'     // Sync work in progress, show big sync progress bar.
    // +'failed_big_sync'   // Something went wrong during initial sync, and the app requires user
                            // interaction to resume sync (user must be in the lobby).
    // 'big_sync_recovered' // The app recovered from failed_big_sync. This should signal to the
                            // user they move into a spot where the app is safe to sync (probably
                            // the lobby).

    // Reasons:
    // 'manual_unauth'      // Sync was manually disabled by rejecting the google drive
                            // authentication dialog.
    // 'local_offline'      // A sync operation failed because we went offline.
    // 'gdrive_offline'     // Communication to google drive failed and all immediate recovery
                            // failed. Assuming google is offline and doing further exponential
                            // backoff until we re-establish connection.

    _setup() {
        profileBegin("SyncFSInit", "Call _setup on SyncFS");
        var _this = this;

        this.status = { state: 'uninitialized', reasons: [] };

        this.initialized = false;
        this.fileSyncIsPaused = false; // Does not affect syncing metadata, just pauses file upload/download;
        this.syncInProgress = false;
        this.isOnline = navigator.onLine;
        this.needsFlushing = true;
        this.masterFlushPromise = undefined;
        this.pushingChangeIdx = -1;

        this.syncMetadataPath = "/.SyncMetadata"; // stored only locally, /idbfs prefix added internally
        this.syncMetadata = undefined;
        this.googleDrive = undefined;

        this.ioActionImpl = {
            "writeFile" : this._GD_writeFile,
            "deleteFile" : this._GD_deleteFile
        };

        var onlineListener = function(event) {
            _this._updateOnlineStatus();
        }

        // Fun fact, these events are not triggered when using throttling in the inspector, 2 year old bug
        // and I doubt anyone will ever fix it. https://bugs.chromium.org/p/chromium/issues/detail?id=422956
        // You gotta kill your internet connection at an OS level to test these.
        window.addEventListener('online',  onlineListener);
        window.addEventListener('offline', onlineListener);

        // This will fail when offline due to errors thrown by gapi, but will create a GoogleDrive
        // object and set up this.googleDrive properly.
        profileBegin("GoogleAuth", "start Google authentication");

        this.initGoogleDrive()
        .then(() => {
            this._debugLog('Google Drive API initialized');
            profileEnd("GoogleAuth", "Google authentication complete");
            this._completeInitializationStep();
        }).catch((error) => {
            console.error(`Failed to initialize Google Drive API: ${error}`);
            profileEnd("GoogleAuth", "Google authentication failed");
            this._completeInitializationStep();
        });

        this.idbToken = 0;
        this.idbPromises = {};
    }

    // Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////

    // Should be called once emscripten has been loaded.
    initializeFileSystem = () => {
        if (this.syncMetadata)
            return;

        profileEnd("SyncRequest", "Sync request received");
        profileBegin("ReadMetadata", "Start reading metadata for syncfs");

        this.idbfsReverseFlush()
        .then(() => {
            return this.readSyncMetadata();
        })
        .then((metadata) => {
            this._debugLog('Done reading sync metadata');
            this.syncMetadata = metadata;
            profileEnd("ReadMetadata", "Metadata received");
            this._completeInitializationStep();
        });
    };

    // Initialization is done in 2 separate phases. Google auth and loading metadata. This waits for
    // both to be completed and notifies unity when done.
    _completeInitializationStep() {
        if (!this.syncMetadata || !this.googleDrive) {
            this._debugLog("_completeInitializationStep - Not yet fully complete [this.syncMetadata: " + (this.syncMetadata ? "Complete" : "N/A" ) + " , this.googleDrive: " + (this.googleDrive ? "Complete": "N/A") + "]" );
            return;
        } // Init not done yet.
        if (this.initialized) return; // already initialized

        this.initialized = true;
        profileEnd("SyncFSInit", "SyncFS initialization fully complete");
        if (this.googleDrive.access_token) {
            MessageHandler.callUnity("SyncFS", "JSInitializationResult", '');
            this.performBigSync();
        } else {
            var target_state = "failed_big_sync";
            var reasons = [];
            if (!this.isOnline) reasons.push("local_offline");
            if (this.googleDrive.hasUserDeclinedAuth)
            {
                // Users declined google access. Stay offline.
                reasons.push("manual_unauth");
            }
            else if (this.googleDrive.access_token == undefined && reasons.length == 0)
            {
                // Authentication error, we can try again right away whenever the user goes to the lobby.
                target_state = "big_sync_recovered";
            }
            else if (reasons.length == 0)
            {
                // If we get here, it probably means this.isOnline is faulty, we already handle all
                // authentication errors above, and reading syncMetadata failures should be solved by now.
                console.error("Failure condition was not handled during auth");
            }

            this.attemptStatusChange(target_state, reasons);
            MessageHandler.callUnity("SyncFS", "JSInitializationResult", 'Error authenticating with google.');
            this.updateBigSyncSyncProgress(100); // Skip big sync and notify unity that it's complete.
        }
    }

    initGoogleDrive = () => {
        var _this = this;
        return new Promise((resolve, reject) => {
            this._debugLog('Initializing Google Drive API');
            var drive = new GoogleDrive(this.timestamp);
            drive.initialize(_this).then(() => {
                _this.googleDrive = drive;
                resolve();
            }, (syncError) => {
                _this.googleDrive = drive;
                reject(syncError);
            });
        });
    };

    reauthenticate = () => {
        if (this.reauthenticating)
            return;
        this.reauthenticating = true;
        this._debugLog("Reauthenticating with Google Drive");
        this.googleDrive.reauthenticate().then(() => {
            this._debugLog("Successfully reauthenticated with Google Drive");
            this.solveStatusReason("manual_unauth");
            MessageHandler.callUnity("SyncFS", "JSReauthenticateResult", true);
        }).catch((authError) => {
            this._debugLog("Failed to reauthenticate with Google Drive");
            MessageHandler.callUnity("SyncFS", "JSReauthenticateResult", false);
        }).then(() => {
            this.reauthenticating = false;
        });
    };

    // Status management
    ////////////////////////////////////////////////////////////////////////////////////////////////

    requestStatusUpdate = () => {
        this.sendStatus();
    };

    // This method may add reasons to the current status, but will not remove reasons.
    // Returns if the transition was possible or if we are already in that state.
    attemptStatusChange(newState, newReasons) {
        // Handle transitioning to the same state
        newReasons = newReasons ? newReasons : [];
        var oldState = this.status.state;
        if (oldState == newState) {
            var changed = false;
            for (var i = newReasons.length - 1; i >= 0; i--) {
                var idx = this.status.reasons.indexOf(newReasons[i]);
                if (idx == -1) {
                    changed = true;
                    this.status.reasons.push(newReasons[i]);
                }
            }
            if (changed) this.sendStatus();
            return true;
        }

        if (this.status.reasons.length > 0) return false;

        // Auto-solve error states when no reasons are presented.
        if (newReasons.length == 0) {
            if (newState == 'failed_big_sync') newState = 'big_sync_recovered';
        }

        // Legal state transition checks:
        var legal = false;
        switch(newState) {
            case 'uninitialized': {
            } break;
            case 'synced': {
                if (oldState == 'synchronizing') legal = true;
            } break;
            case 'synchronizing': {
                if (oldState == 'synced' || oldState == 'offline' || oldState == 'doing_big_sync') legal = true;
            } break;
            case 'offline': {
                if (oldState == 'synced' || oldState == 'synchronizing') legal = true;
            } break;
            case 'doing_big_sync': {
                if (oldState == 'uninitialized' || oldState == 'big_sync_recovered') legal = true;
            } break;
            case 'failed_big_sync': {
                if (oldState == 'doing_big_sync' ||
                    oldState == 'uninitialized' || oldState == 'big_sync_recovered') legal = true;
            } break;
            case 'big_sync_recovered': {
                if (oldState == 'failed_big_sync' || oldState == 'uninitialized' ||
                    oldState == 'doing_big_sync') legal = true;
            } break;
        }
        if (legal == false) return false;

        // Do actual transition.
        this.status.state = newState;
        this.status.reasons = newReasons;
        this.sendStatus();
        if (window.ENABLE_DEBUG) {
            this._debugLog("attemptStatusChange - newState: '" + newState + "' - reasons: " + JSON.stringify(newReasons) + "");
        }

        /* LECQA-179: This will start retrying reconnect - work in progress
        if (newReasons.indexOf("gdrive_offline") !== -1) {
            this.googleDrive.attemptGoogleReconnect();
        }
        */
        return true;
    }

    errorStateForStatus(status)
    {
        if (status == 'doing_big_sync' || status == 'failed_big_sync' ||
            status == 'uninitialized' || status == 'big_sync_recovered') return 'failed_big_sync';
        return 'offline';
    }

    solveStatusReason = (reason) => {
        var idx = this.status.reasons.indexOf(reason);
        if (idx == -1) return;
        this.status.reasons[idx] = this.status.reasons[this.status.reasons.length-1];
        this.status.reasons.pop();

        if (this.status.reasons.length == 0) {
            if (this.status.state == "failed_big_sync") this.attemptStatusChange("big_sync_recovered");
            if (this.status.state == "offline") {
                this.attemptStatusChange("synchronizing");
                this.pushNextChange();
            }
        } else {
            this.sendStatus();
        }
    };

    _updateOnlineStatus() {
        // if (this.isOnline == navigator.onLine) return;

        console.log("CHANGE ONLINE STATUS", navigator.onLine);
        this.isOnline = navigator.onLine;
        if (this.isOnline) {
            this.solveStatusReason("local_offline");
        } else {
            this.attemptStatusChange(this.errorStateForStatus(this.status.state), ["local_offline"]);
        }
    }

    pauseFileSync = () => {
        this.fileSyncIsPaused = true;
    };

    resumeFileSync = () => {
        this.fileSyncIsPaused = false;
        this.pushNextChange();
    };

    sendStatus = () => {
        MessageHandler.callUnity("SyncFS", "JSSyncStatusChanged", JSON.stringify(this.status));
    };

    // IO action registry
    ////////////////////////////////////////////////////////////////////////////////////////////////

    // Action types:
    // writeFile, deleteFile
    addIOAction = (actionType, paths) => {
        if (actionType !== 'writeFile' && actionType !== 'deleteFile') {
            this._debugLog(`Unrecognized sync action type ${actionType}!`);
            return;
        }

        var fileId = this._getFileID(paths[0]);
        if (actionType === 'deleteFile') {
            this.removePendingWriteActions(paths[0]);
            if (!fileId) {
                this._debugLog(`Canceling remote delete action for file ${paths[0]} - the file does not exist on Google Drive thus the request would have resulted in 403/404`);
                return;
            }
        }

        var changes = this.syncMetadata.pendingLocalChanges;
        // merge subsequent writes to the same file into one action;
        // any different operation performed on the file breaks the chain of merged actions;
        // [writeFile:A, writeFile:A] => [writeFile:A] (merged)
        // [writeFile:A, deleteFile:A, writeFile:A] => [writeFile:A, deleteFile:A, writeFile:A] (no merging)
        // we could do more robust change merging to minimize network traffic but it doesn't seem worth it for now
        var action;
        if (actionType === 'writeFile') {
            // find the last action performed on the given file
            for (var i = changes.length - 1; i >= 0; --i) {
                // don't merge with changes being currently pushed
                if (i == this.pushingChangeIdx)
                    continue;

                if (changes[i].paths[0] === paths[0]) {
                    if (changes[i].type === 'writeFile') {
                        changes[i].timestamp = this.timestamp();
                        action = changes[i];
                    }
                    break;
                }
            }
        }
        if (!action) {
            action = {
                type: actionType,
                paths: paths,
                timestamp: this.timestamp(),
                fileId: fileId
            };
            changes.push(action);

            this._debugLog(`Added IO action: ${action.type} ${action.paths[0]} ${action.paths[1]} ${action.timestamp}`, this.syncMetadata.pendingLocalChanges);
        }

        this.writeSyncMetadata(this.syncMetadata);
        if (this.pushingChangeIdx == -1 && !this.fileSyncIsPaused) {
            this.pushNextChange();
        }

        this.signalFlushRequired();
        this.ensureStateIsFlushed();

        return action;
    };

    removePendingWriteActions = (path) => {
        var pushingChange;
        if (this.pushingChangeIdx != -1) {
            pushingChange = this.syncMetadata.pendingLocalChanges[this.pushingChangeIdx];
        }

        this.syncMetadata.pendingLocalChanges = this.syncMetadata.pendingLocalChanges.filter((change) => {
            // only remove pending write actions
            if (change.type !== 'writeFile')
                return true;
            // don't remove the change currently being pushed
            if (change == pushingChange)
                return true;
            // remove the write action if the path matches
            if (change.paths[0] === path) {
                this._debugLog(`Removing pending write action (file deleted): ${change.paths[0]}, ${change.timestamp}`);
                return false;
            }
            // keep other writes in the queue
            return true;
        });

        if (pushingChange) {
            this.pushingChangeIdx = this.syncMetadata.pendingLocalChanges.indexOf(pushingChange);
        }
    };

    // File operations
    ////////////////////////////////////////////////////////////////////////////////////////////////

    saveFile = (path) => {
        if (!this._initializationCheck())
            return;
        path = this.removeIdbfsFromPath(this._normalizePath(path));
        var action = this.addIOAction('writeFile', [path]);
        this.updateFileModifiedTime(path, action.timestamp, true);
    };

    updateFileModifiedTime = (path, modifiedTime, saveMetadata) => {
        var file = this.syncMetadata.files[path];
        if (!file) {
            file = {name: path};
            this.syncMetadata.files[path] = file;
        }
        file.modifiedTime = modifiedTime;
        if (saveMetadata) {
            this.writeSyncMetadata(this.syncMetadata);
        }
        this._debugLog(`Updated modified time of file ${path} to ${modifiedTime}`);
    };

    createDirectoryTree = (path) => {
        // Do nothing, directory structure is stored implicitly in file names, e.g. file.name="a/b/c/file.txt"
    };

    deleteFile = (path) => {
        if (!this._initializationCheck())
            return;
        path = this.removeIdbfsFromPath(this._normalizePath(path));
        this._deleteFile(path);
        this.writeSyncMetadata(this.syncMetadata);
    };

    copyFile = (sourcePath, destPath) => {
        if (!this._initializationCheck())
            return;
        sourcePath = this.removeIdbfsFromPath(this._normalizePath(sourcePath));
        destPath = this.removeIdbfsFromPath(this._normalizePath(destPath));
        this._copyFile(sourcePath, destPath, true);
    };

    moveFile = (sourcePath, destPath) => {
        if (!this._initializationCheck())
            return;
        sourcePath = this.removeIdbfsFromPath(this._normalizePath(sourcePath));
        destPath = this.removeIdbfsFromPath(this._normalizePath(destPath));
        this._moveFile(sourcePath, destPath, true);
    };

    deleteDirectory = (path) => {
        if (!this._initializationCheck())
            return;

        path = this.removeIdbfsFromPath(this._normalizePath(path)) + "/";

        var keys = Object.keys(this.syncMetadata.files);
        for (var idx = 0; idx < keys.length; ++idx) {
            var key = keys[idx];
            if (key.startsWith(path)) {
                this._deleteFile(key);
            }
        }

        this.writeSyncMetadata(this.syncMetadata);
    };

    copyDirectory = (sourcePath, destPath) => {
        if (!this._initializationCheck())
            return;

        sourcePath = this.removeIdbfsFromPath(this._normalizePath(sourcePath)) + "/";
        destPath = this.removeIdbfsFromPath(this._normalizePath(destPath)) + "/";

        var len = sourcePath.length;
        var keys = Object.keys(this.syncMetadata.files);
        for (var idx = 0; idx < keys.length; ++idx) {
            var key = keys[idx];
            if (key.startsWith(sourcePath)) {
                var destFilePath = destPath + key.substr(len);
                this._copyFile(key, destFilePath, false);
            }
        }

        this.writeSyncMetadata(this.syncMetadata);
    };

    moveDirectory = (sourcePath, destPath) => {
        if (!this._initializationCheck())
            return;

        sourcePath = this.removeIdbfsFromPath(this._normalizePath(sourcePath)) + "/";
        destPath = this.removeIdbfsFromPath(this._normalizePath(destPath)) + "/";

        var len = sourcePath.length;
        var keys = Object.keys(this.syncMetadata.files);
        for (var idx = 0; idx < keys.length; ++idx) {
            var key = keys[idx];
            if (key.startsWith(sourcePath)) {
                var destFilePath = destPath + key.substr(len);
                this._moveFile(key, destFilePath, false);
            }
        }

        this.writeSyncMetadata(this.syncMetadata);
    };

    // Private helpers
    ////////////////////////////////////////////////////////////////////////////////////////////////

    _debugLog = (...args) => {
        console.log(Array.from(args).map((a) => {
            try {
                return JSON.parse(JSON.stringify(a));
            } catch (e) {
                return a;
            }
        }));
    };

    _initializationCheck = () => {
        if (this.initialized) {
            return true;
        } else {
            this._debugLog('Google Drive API is not initialized yet! Ignoring the call.');
            return false;
        }
    };

    _normalizePath = (path) => {
        if (typeof path !== 'string') {
            return path; // silently fail and do nothing.
        }
        path = ('/' + path).replace(/[\\\/]+/g, '/');
        path = path.replace(/\/$/, '');
        return path;
    };

    _getFileID = (path) => {
        return this.syncMetadata.files[path] ?
            this.syncMetadata.files[path].id :
            undefined;
    };

    fetchRemoteFileList = () => {
        return new Promise((resolve, reject) => {
            this._debugLog(`Fetching remote file list`);
            this.googleDrive.getFileList("").then((files) => {
                var allFiles = {};
                files.forEach(function(file) {
                    if (file) {
                        allFiles[file.name] = file;
                    }
                });
                resolve(allFiles);
            }, (syncError) => {
                this._debugLog(`Failed to fetch remote file list: ${syncError}`);
                reject(syncError);
            });
        });
    };

    _deleteFile = (path) => {
        this.addIOAction('deleteFile', [path]);
        delete this.syncMetadata.files[path];
    };

    _copyFile = (sourcePath, destPath, saveMetadata) => {
        var action = this.addIOAction('writeFile', [destPath]);
        // will create new File entry with destPath and given timestamp,
        // id is going to be updated based on the response from Google Drive
        this.updateFileModifiedTime(destPath, action.timestamp, saveMetadata);
    };

    _moveFile = (sourcePath, destPath, saveMetadata) => {
        this.addIOAction('deleteFile', [sourcePath]);
        var action = this.addIOAction('writeFile', [destPath]);

        this.syncMetadata.files[destPath] = this.syncMetadata.files[sourcePath];
        delete this.syncMetadata.files[sourcePath];
        this.syncMetadata.files[destPath].name = destPath;
        delete this.syncMetadata.files[destPath].id;
        this.updateFileModifiedTime(destPath, action.timestamp, saveMetadata);
    };

    // Realtime synchronization to Google Drive
    ////////////////////////////////////////////////////////////////////////////////////////////////

    // TODO(Catalin): Error handling and quota checks.
    _GD_writeFile = (timestamp, fileId, path) => {
        return new Promise((resolve, reject) => {
            this.idbfsReadRawFile(path).then((data) => {
                var fileId = this._getFileID(path);
                if (data.byteLength < window.GD_RESUMABLE_UPLOAD_SIZE_THRESHOLD) {
                    if (window.ENABLE_DEBUG) {
                        this._debugLog(`_GD_writeFile :: Uploading file ${path} (${data.byteLength} bytes) as multipart upload`);
                    }
                    return this.googleDrive.uploadFile(fileId, path, data, timestamp);
                } else {
                    if (window.ENABLE_DEBUG) {
                        this._debugLog(`_GD_writeFile :: Uploading file ${path} (${data.byteLength} bytes) as resumable upload`);
                    }
                    return this.googleDrive.uploadFileResumable(fileId, path, data, timestamp);
                }
            }).then((metadata) => {
                this._debugLog(`Uploaded file ${path}`, metadata);
                this.syncMetadata.files[path] = metadata;
                this.writeSyncMetadata(this.syncMetadata);
                resolve(metadata);
            }).catch((syncError) => {
                this._debugLog(`Failed to upload file ${path}`, syncError);
                reject(syncError);
            });
        });
    };

    _GD_deleteFile = (timestamp, fileId, path) => {
        return new Promise((resolve, reject) => {
            this._debugLog('Delete GD file: ', path, fileId);
            this.googleDrive.deleteFile(fileId, true).then((metadata) => {
                this._debugLog(`Deleted Google Drive file ${path} (${fileId})`, metadata);
                resolve();
            }, (syncError) => {
                this._debugLog(`Failed to delete GD file ${path} (${fileId})`, syncError);
                reject(syncError);
            });
        });
    };

    _GD_copyFile = (timestamp, fileId, sourcePath, destPath) => {
        return this._GD_writeFile(timestamp, undefined, destPath);
    };

    _GD_moveFile = (timestamp, fileId, sourcePath, destPath) => {
        return this._GD_deleteFile(timestamp, fileId, sourcePath).then(() => {
            return this._GD_writeFile(timestamp, undefined, destPath);
        }).then((metadata) => {
            resolve(metadata);
        }).catch((syncError) => {
            reject(syncError);
        });
    };

    pushNextChange = (changeIndex) => {
        if (this.pushingChangeIdx != -1 || this.syncInProgress) return;
        if (!this.attemptStatusChange("synchronizing")) return; // If we can't get into a "synchronizing" state, bail.
        if (this.syncMetadata.pendingLocalChanges.length === 0) {
            this.attemptStatusChange("synced");
            return;
        }

        var _this = this;

        _this.pushingChangeIdx = changeIndex ? changeIndex : 0;
        var change = _this.syncMetadata.pendingLocalChanges[_this.pushingChangeIdx];

        function doActualPush() {
            _this._debugLog(`Pushing change: ${change.paths[0]}`, change);

            // perform the remote IO action
            var actionPromise = _this.ioActionImpl[change.type].apply(this, [change.timestamp, change.fileId].concat(change.paths));
            actionPromise.then((result) => {
                // remove the action from the queue and persist the queue
                _this.syncMetadata.pendingLocalChanges.shift();
                _this.writeSyncMetadata(_this.syncMetadata);
                // proceed with the next pending change
                _this.finishPushingChange(_this.pushingChangeIdx);
            }, (syncError) => {
                _this.abortPushingChanges(syncError);
            });
        }

        _this.ensureStateIsFlushed().then(doActualPush);
    };

    finishPushingChange = (nextChangeIdx) => {
        this.pushingChangeIdx = -1;
        if (nextChangeIdx < this.syncMetadata.pendingLocalChanges.length) {
            this.pushNextChange(nextChangeIdx);
        } else {
            this.idbfsFlushChanges().then(() => {
                this.attemptStatusChange("synced");
            });
        }
    };

    abortPushingChanges(syncError) {
        this.pushingChangeIdx = -1;
        console.log("abortPushingChanges: " + syncError.message);
        if (syncError.message == "The user did not approve access.")
            this.attemptStatusChange("offline", ["manual_unauth"]);
        else
            this.attemptStatusChange("offline", ["gdrive_offline"]);
    }

    // Big sync
    ////////////////////////////////////////////////////////////////////////////////////////////////

    performBigSync = () => {
        this._debugLog("performBigSync()");
        profileBegin("BigSync", "Big sync request received, starting");

        if (!this._initializationCheck())
            return;

        if (this.syncInProgress) {
            this._debugLog("performBigSync(): sync is already in progress, ignoring...");
            return;
        }

        if (!this.attemptStatusChange("doing_big_sync")) {
            this.failBigSync(new SyncError("transition_impossible"));
            return;
        }

        this.syncInProgress = true;

        this.updateBigSyncSyncProgress(0);

        // hosts the sync state
        var sync = {};

        // get local file list (sync metadata should be already loaded at this point)
        sync.localFiles = JSON.parse(JSON.stringify(this.syncMetadata.files)); // copy the files registry as localFiles is meant to be a temporary, local object
        // this._debugLog("Local files:", sync.localFiles);
        // compute local delete list
        sync.localDeleteList = this.computeLocalDeleteList(this.syncMetadata.pendingLocalChanges);
        this._debugLog("Local delete list:", sync.localDeleteList);

        profileBegin("BS_Flush", "Flush to ensure correct state");
        // Ensure correct local state.
        this.idbfsFlushChanges().then(() => {
            profileEnd("BS_Flush", "End flush");
            MessageHandler.callUnity("SyncFS", "JSMetadataStepComplete");
            profileBegin("BS_RemoteList", "Get remote file list");
            return this.fetchRemoteFileList();
        })
        // fetch remote file list
        .then((files) => {
            profileEnd("BS_RemoteList", "End remote list");
            profileBegin("BS_RemoteChanges", "Get remote changes since last sync");
            this.updateBigSyncSyncProgress(10);
            sync.remoteFiles = files;
            this._debugLog("Remote file list fetched, fetching remote changes");
            return this.fetchRemoteChangesSinceLastSync();
        })
        // fetch remote change list since last sync
        .then((changeResponse) => {
            profileEnd("BS_RemoteChanges", "End remote changes");
            profileBegin("BS_Changes", "Write metadata and compute changes");
            this.updateBigSyncSyncProgress(20);
            this._debugLog("Remote changes response:", changeResponse);
            // remember the current start page token for the next sync
            this.syncMetadata.lastStartPageToken = changeResponse.newStartPageToken;
            this.writeSyncMetadata(this.syncMetadata);
            // generate remote delete list
            sync.remoteDeleteList = this.computeRemoteDeleteList(changeResponse.changes);
            this._debugLog("Remote delete list:", sync.remoteDeleteList);
        })
        // calculate changes that need to be applied between file systems
        .then(() => {
            this._debugLog("Calculating differences between remote FS and local FS");
            sync.remoteToLocalOps = this.mergeFileSystems(sync.remoteFiles, sync.remoteDeleteList, sync.localFiles, sync.localDeleteList, true);
            this._debugLog("Calculating differences between local FS and remote FS");
            sync.localToRemoteOps = this.mergeFileSystems(sync.localFiles, sync.localDeleteList, sync.remoteFiles, sync.remoteDeleteList, false);
            // prepare progress data
            var numTotalOps = sync.remoteToLocalOps.length + sync.localToRemoteOps.length;
            var numCompletedOps = 0;
            sync.completeSyncMergeOperation = () => {
                ++numCompletedOps;
                this.updateBigSyncSyncProgress(20 + Math.round(70 * numCompletedOps / numTotalOps));
            }
        })
        // merge remote FS changes into local FS
        .then(() => {
            profileEnd("BS_Changes", "End computing changes");
            profileBegin("BS_MergeAndMeta", "Start merging, writing metadata and flush"); // merging is really short, clutters the profier.
            this._debugLog("Merging remote FS changes into local FS");
            return Promise.all(
                    sync.remoteToLocalOps.map((op) => {
                        return op.func.apply(this, op.params)
                                .then(() => {
                            sync.completeSyncMergeOperation();
                        });
                    }))
                    .catch((e) => {
                        this._debugLog("Merging remote FS changes into local FS FAILED!! -- " + JSON.stringify(e));
                    });
        })
        // merge local FS changes into remote FS
        .then(() => {
            this._debugLog("Merging local FS changes into remote FS");
            return Promise.all(
                    sync.localToRemoteOps.map((op) => {
                        return op.func.apply(this, op.params)
                                .then(() => {
                            sync.completeSyncMergeOperation();
                        });
                    }))
                    .catch((e) => {
                        this._debugLog("Merging local FS changes into remote FS FAILED!! -- " + JSON.stringify(e));
                    });
        })
        .catch((syncError) => {
            this._debugLog("Sync failed", syncError);
            this.failBigSync(syncError, true);

            throw new SyncError("skip_writing_metadata"); // Skip the other methods in the promise.
        })
        .then(() => {
            // update sync metadata with remote file list, the filesystems should be the same at this point
            this.syncMetadata.files = sync.remoteFiles;
            // clear local pending changes queue - at this points both filesystems should be the same
            this.syncMetadata.pendingLocalChanges = [];
            this.writeSyncMetadata(this.syncMetadata);

            return this.idbfsFlushChanges();
        })
        .then(() => {
            profileEnd("BS_MergeAndMeta", "End writing metadata and flush");
            this.finishBigSync();
        })
        .catch((syncError) => {
            if (syncError.message != "skip_writing_metadata") {
                this._debugLog("Failed to write metadata:", syncError); // NOTE(Catalin): I don't know if this is possible, it should not be...
                throw "Sync succeded, but something failed while writing metadata.";
            }
        });
    };

    failBigSync = (syncError, reverseFlushNeeded) => {
        // Revert sync changes
        var _this = this;
        var revertChanges = function() {
            _this._debugLog("Big sync failed! - " + JSON.stringify(syncError));

            _this.syncInProgress = false;
            if (syncError.message != "transition_impossible")
            {
                // TODO(Catalin): Other reasons may occur here?
                if (syncError.message == "The user did not approve access.")
                    _this.attemptStatusChange("failed_big_sync", ["manual_unauth"]);
                else if (_this.googleDrive.access_token == undefined)
                    // No automatic recovery mechanism, but users have to manually restart big sync,
                    // as they have to do authentication again.
                    _this.attemptStatusChange("big_sync_recovered");
                else
                {
                    _this.attemptStatusChange("failed_big_sync", ["gdrive_offline"]);
                }

            }

            // Unity expects a "JSBigSyncFinished" event to exit loading.
            _this.updateBigSyncSyncProgress(100);
        };

        if (reverseFlushNeeded) {
            this.idbfsReverseFlush().then(revertChanges);
        } else {
            revertChanges();
        }
    };

    finishBigSync = () => {
        profileEnd("BigSync", "Successfully completed big sync");
        this._debugLog("Finished big sync!");

        // finish big sync
        this.syncInProgress = false;
        this.updateBigSyncSyncProgress(100);

        // activate realtime sync
        MessageHandler.callWebGL('SyncFS_IDB', 'startTrackingFileChanges', []);
        this.attemptStatusChange("synchronizing"); // TODO(Catalin): ok, see if/how status changes should trigger synchronization.
        this.pushNextChange();
    };

    updateBigSyncSyncProgress = (progress) => {
        if (progress === 0) {
            MessageHandler.callUnity("SyncFS", "JSBigSyncStarted");
        }
        MessageHandler.callUnity("SyncFS", "JSBigSyncProgress", progress);
        if (progress === 100) {
            MessageHandler.callUnity("SyncFS", "JSBigSyncFinished");
        }

        if (this.$splashscreen) {
            this.$splashscreen.updateSyncProgress(progress / 100);
        }
    };

    // Flushing
    ////////////////////////////////////////////////////////////////////////////////////////////////

    signalFlushRequired = () => {
        this.needsFlushing = true;
        this.internalNeedsFlushing = true;
    };

    ensureStateIsFlushed = () => {
        var _this = this;
        if (!_this.internalNeedsFlushing) return Promise.resolve();

        _this.flushPromises = _this.flushPromises ? _this.flushPromises : [];

        var p = new Promise((resolve, reject) => {
            _this.flushPromises.push({ resolve: resolve, reject: reject });
        });

        if (_this.masterFlushPromise == undefined)
        {
            _this.masterFlushPromise = new Promise((resolve, reject) => {
                function doFlush() {
                    _this.needsFlushing = false;
                    _this.idbfsFlushChanges().then(() => {
                        if (!_this.needsFlushing) {
                            _this.internalNeedsFlushing = false;
                            resolve();
                        } else {
                            window.setTimeout(doFlush, 50);
                        }
                    });
                }

                doFlush();
            });
        }

        _this.masterFlushPromise.then(() => {
            var len = _this.flushPromises.length;
            for (var i = len - 1; i >= 0; i--) {
                _this.flushPromises[i].resolve();
            }
            _this.flushPromises = [];
            _this.masterFlushPromise = undefined;
        });

        return p;
    };

    // IndexedDB IO helpers

    removeIdbfsFromPath(path) {
        return path.replace(/^\/idbfs/, '');
    }

    idbfsGetPromiseToken = (resolve, reject) => {
        var token = this.idbToken++;
        this.idbPromises[token] = {
            resolve: resolve,
            reject: reject
        };
        return token;
    };

    idbfsResolvePromise = (token, result) => {
        this.idbPromises[token].resolve(result);
        delete this.idbPromises[token];
    };

    idbfsRejectPromise = (token, error) => {
        this.idbPromises[token].reject(error);
        delete this.idbPromises[token];
    };

    idbfsCreateDirectory = (path) => {
        var idbfsPath = "/idbfs" + path;
        MessageHandler.callWebGL('SyncFS_IDB', 'createDirectory', [idbfsPath]);
    };

    idbfsReadRawFile = (path) => {
        return new Promise((resolve, reject) => {
            var token = this.idbfsGetPromiseToken(resolve, reject);
            MessageHandler.callWebGL('SyncFS_IDB', 'readRawFile', ['/idbfs' + path, token]);
        });
    };

    idbfsReadTextFile = (path) => {
        return new Promise((resolve, reject) => {
            var token = this.idbfsGetPromiseToken(resolve, reject);
            MessageHandler.callWebGL('SyncFS_IDB', 'readTextFile', ['/idbfs' + path, token]);
        });
    };

    idbfsWriteRawFile = (path, data) => {
        return new Promise((resolve, reject) => {
            var token = this.idbfsGetPromiseToken(resolve, reject);
            MessageHandler.callWebGL('SyncFS_IDB', 'writeRawFile', ['/idbfs' + path, data, token]);
        });
    };

    idbfsWriteTextFile = (path, contents) => {
        return new Promise((resolve, reject) => {
            var token = this.idbfsGetPromiseToken(resolve, reject);
            MessageHandler.callWebGL('SyncFS_IDB', 'writeTextFile', ['/idbfs' + path, contents, token]);
        });
    };

    idbfsRemoveFile = (path) => {
        return new Promise((resolve, reject) => {
            MessageHandler.callWebGL('SyncFS_IDB', 'removeFile', ['/idbfs' + path]);
            resolve();
        });
    };

    idbfsReverseFlush() {
        return new Promise((resolve, reject) => {
            var token = this.idbfsGetPromiseToken(resolve, reject);
            MessageHandler.callWebGL('SyncFS_IDB', 'reverseFlush', [token]);
        });
    }

    idbfsFlushChanges = () => {
        return new Promise((resolve, reject) => {
            var token = this.idbfsGetPromiseToken(resolve, reject);
            MessageHandler.callWebGL('SyncFS_IDB', 'flushChanges', [token]);
        });
    };

    // IndexedDB raw IO callbacks

    IDB_OnWriteComplete = (path, context) => {
        this.idbfsResolvePromise(context);
    };

    IDB_OnReadComplete = (path, result, context) => {
        if (result) {
            this.idbfsResolvePromise(context, result);
        } else {
            this.idbfsRejectPromise(context, new SyncError(`Failed to read file ${path}`));
        }
    };

    IDB_OnFileRemoved = (path) => {
    };

    IDB_OnDirectoryCreated = (path) => {
    };

    IDB_OnDirectoryRemoved = (path) => {
    };

    IDB_OnTrackedFileModified = (path) => {
        this.saveFile(path);
    };

    IDB_OnChangesFlushed = (context) => {
        this.idbfsResolvePromise(context);
    };

    // Sync metadata

    readSyncMetadata = () => {
        return new Promise((resolve, reject) => {
            this.idbfsReadTextFile(this.syncMetadataPath)
                .then((fileContent) => {
                    var metadata = JSON.parse(fileContent);
                    resolve(metadata);
                }).catch((error) => {
                    console.warn("Failed to read sync metadata - doesn't exist or is unreadable. Assuming empty metadata.", error);
                    var defaultMetadata = {
                        // last startPageToken returned from the GD changes service
                        lastStartPageToken: "",
                        // local changes (IO actions) awaiting to be synced with GD
                        pendingLocalChanges: [],
                        // file full path => File object
                        files: {}
                    };
                    resolve(defaultMetadata);
                });
        });
    };

    writeSyncMetadata = (metadata) => {
        var metadataContents = JSON.stringify(metadata);
        // this._debugLog("Sync metadata:", metadata, metadataContents);
        return this.idbfsWriteTextFile(this.syncMetadataPath, metadataContents);
    };

    // Big sync operations

    // generates RFC 3339 compliant timestamp used by Google Drive REST API;
    // always returns values in UTC
    timestamp = () => {
        function dateStringRFC3339(date) {
            function pad(n) {
                return n < 10 ? '0' + n : n;
            }
            return date.getUTCFullYear() + '-' +
                pad(date.getUTCMonth() + 1) + '-' +
                pad(date.getUTCDate()) +
                'T' +
                pad(date.getUTCHours()) + ':' +
                pad(date.getUTCMinutes()) + ':' +
                pad(date.getUTCSeconds()) + 'Z';
        }
        return dateStringRFC3339(new Date());
    };

    fetchRemoteChangesSinceLastSync = () => {
        if (this.syncMetadata.lastStartPageToken.length > 0) {
            return this.googleDrive.getChangeList(this.syncMetadata.lastStartPageToken);
        } else {
            return this.googleDrive.getChangeStartPageToken().then((response) => {
                return Promise.resolve({
                    changes: [],
                    newStartPageToken: response.startPageToken
                });
            });
        }
    };

    computeLocalDeleteList = (changes) => {
        var removedFileIds = [];
        changes.forEach((change) => {
            if (change.type === "deleteFile" && change.fileId) {
                removedFileIds.push(change.fileId);
            }
        });
        return removedFileIds;
    };

    computeRemoteDeleteList = (changes) => {
        var removedFileIds = [];
        changes.forEach((change) => {
            if (change.removed) {
                removedFileIds.push(change.fileId);
            }
        });
        return removedFileIds;
    };

    // returns a list of merge operations
    mergeFileSystems = (srcFiles, srcDeleteList, destFiles, destDeleteList, srcIsRemote) => {
        var srcFilePaths = Object.keys(srcFiles);
        var destFilePaths = Object.keys(destFiles);
        var ops = [];
        var filesToDelete = [];
        srcFilePaths.forEach((srcFilePath) => {
            var srcFile = srcFiles[srcFilePath];
            var op;
            if (destFilePaths.includes(srcFilePath)) {
                // file exists in destination file system
                var destFile = destFiles[srcFilePath];
                var srcFileModifiedDate = Date.parse(srcFile.modifiedTime);
                var destFileModifiedDate = Date.parse(destFile.modifiedTime);
                if (srcFileModifiedDate !== destFileModifiedDate) {
                    // files are different, overwrite the older one with the newer one
                    if (srcFileModifiedDate > destFileModifiedDate) {
                        // copy from src to dest
                        op = {func: this.copyFileToFilesystem, params: [srcFile, srcIsRemote, srcIsRemote ? srcFile.id : destFile.id]};
                        destFile.modifiedTime = srcFile.modifiedTime;
                    } else {
                        // copy from dest to src
                        op = {func: this.copyFileToFilesystem, params: [srcFile, !srcIsRemote, srcIsRemote ? srcFile.id : destFile.id]};
                        srcFile.modifiedTime = destFile.modifiedTime;
                    }
                }
            } else {
                // file doesn't exist in destination file system
                if (destDeleteList.includes(srcFile.id)) {
                    // file is in the destination FS deletion list - remove from source FS
                    op = {func: this.deleteFileFromFilesystem, params: [srcFile, srcIsRemote]};
                    filesToDelete.push(srcFilePath);
                } else {
                    // file is NOT in the destination FS deletion list - copy from source FS to destination FS
                    op = {func: this.copyFileToFilesystem, params: [srcFile, srcIsRemote]};
                    destFiles[srcFilePath] = srcFile;
                }
            }
            if (op) {
                ops.push(op);
            }
        });
        filesToDelete.forEach((srcFilePath) => {
            delete srcFiles[srcFilePath];
        });
        return ops;
    };

    copyFileToFilesystem = (file, download, remoteFileId) => {
        if (download) {
            // download file from remote into local
            this._debugLog(`copyFileToFilesystem: downloading ${file.name}`);
            // this._debugLog(`copyFileToFilesystem: downloading ${file.name}`, file);
            return this.googleDrive.downloadFile(file.id, file.name)
                .then((data) => {
                    return this.idbfsWriteRawFile(file.name, data);
                }).then(() => {
                    // notify Unity that Chrome-app specific PlayerPrefs file has been changed
                    if (file.name === "/.ChromePlayerPrefs") {
                        MessageHandler.callUnity("SyncFS", "JSReloadPlayerPrefs");
                    }
                });
        } else {
            // upload file from local to remote
            this._debugLog(`copyFileToFilesystem: uploading ${file.name} (remote file ID: ${remoteFileId})`);
            return this.idbfsReadRawFile(file.name)
                .then((data) => {
                    if (data.byteLength < window.GD_RESUMABLE_UPLOAD_SIZE_THRESHOLD) {
                        if (window.ENABLE_DEBUG) {
                            this._debugLog(`copyFileToFilesystem :: copyFileToFilesystem - download: '${download}' - ${file.name} (${data.byteLength} bytes) as multipart upload`);
                        }
                        return this.googleDrive.uploadFile(remoteFileId, file.name, data, file.modifiedTime);
                    } else {
                        if (window.ENABLE_DEBUG) {
                            this._debugLog(`copyFileToFilesystem :: copyFileToFilesystem - download: '${download}' - ${file.name} (${data.byteLength} bytes) as resumable upload`);
                        }
                        return this.googleDrive.uploadFileResumable(remoteFileId, file.name, data, file.modifiedTime);
                    }
                });
        }
    };

    deleteFileFromFilesystem = (file, isRemoteFS) => {
        if (isRemoteFS) {
            // perform delete request
            this._debugLog(`deleteFileFromFilesystem: deleting remote file ${file.name}`);
            return this.googleDrive.deleteFile(file.id, false);
        } else {
            // remove from IndexedDB
            this._debugLog(`deleteFileFromFilesystem: deleting local file ${file.name}`);
            return this.idbfsRemoveFile(file.name);
        }
    };
}



