/**
 * gapi-chrome-apps version 0.001
 *
 * Provides the Google API javascript client 'gapi' as
 * appropriate for hosted websites, or if in a Chrome packaged
 * app implement a minimal set of functionality that is Content
 * Security Policy compliant and uses the chrome identity api.
 *
 * https://github.com/GoogleChrome/chrome-app-samples/tree/master/gapi-chrome-apps-lib
 *
 */
"use strict";

export function configureGoogleAPI(gapiLoadedCallback) {
  if (typeof gapi !== 'undefined')
    throw new Error('gapi already defined.');
  if (typeof gapiLoadedCallback !== 'function')
    throw new Error('gapiLoadedCallback function must be defined');

  // If not running in a chrome packaged app, load web gapi:
  if (!(chrome && chrome.app && chrome.app.runtime)) {
    window.gapiIsLoaded = function() {
      gapiLoadedCallback();
    };
    // Load web gapi.
    var script = document.createElement('script');
    script.src = 'https://apis.google.com/js/client.js?onload=gapiIsLoaded';
    document.documentElement.appendChild(script);
    return;
  }

  window.gapi = {};
  window.gapi.auth = {};
  window.gapi.client = {};

  window.ENABLE_DEBUG = false;

  var access_token = undefined;

  gapi.auth.authorize = function (params, callback) {
    if (typeof callback !== 'function')
      throw new Error('callback required');

    var details = {};
    details.interactive = params.immediate === false || false;
    if (params.accountHint) {
      // Specifying this prevents the account chooser from appearing on Android.
      details.accountHint = params.accountHint;
    }
    console.assert(!params.response_type || params.response_type == 'token');

    var callbackWrapper = function (getAuthTokenCallbackParam) {
      access_token = getAuthTokenCallbackParam;
      if (typeof access_token !== 'undefined')
        callback({ access_token: access_token });
      else
        callback({ error: chrome.runtime.lastError });
    };

    chrome.identity.getAuthToken(details, callbackWrapper);
  };


  gapi.client.request = function (args) {
    if (typeof args !== 'object')
      throw new Error('args required');
    if (typeof args.callback !== 'function')
      throw new Error('callback required');
    if (typeof args.errorCallback !== 'function')
      throw new Error('errorCallback required');
    if (typeof args.path !== 'string')
      throw new Error('path required');

    if (args.root && args.root === 'string') {
      var path = args.root + args.path;
    } else {
      var path = 'https://www.googleapis.com' + args.path;
    }

    if (typeof args.params === 'object') {
      for (var paramName in args.params) {
        if (path.indexOf(encodeURIComponent(paramName)) !== -1) {
          continue;
        }
        var deliminator = path.indexOf('?') === -1 ? '?' : '&';
        path += deliminator + encodeURIComponent(paramName) + "="
          + encodeURIComponent(args.params[paramName]);
      }
    }

    var method = args.method || 'GET';

    var xhrReadyStates = [];
    xhrReadyStates[XMLHttpRequest.UNSENT] = 'UNSENT';
    xhrReadyStates[XMLHttpRequest.OPENED] = 'OPENED';
    xhrReadyStates[XMLHttpRequest.HEADERS_RECEIVED] = 'HEADERS_RECEIVED';
    xhrReadyStates[XMLHttpRequest.LOADING] = 'LOADING';
    xhrReadyStates[XMLHttpRequest.DONE] = 'DONE';

    var xhr = new XMLHttpRequest();
    xhr.onreadystatechange = () => {

      if (window.ENABLE_DEBUG && typeof console.debug === 'function') {
        console.debug('XHR :: onreadystatechange - new readyState: ' + xhr.readyState + ' ('+ xhrReadyStates[xhr.readyState] +') - path: ' + path);
      }
    };

    xhr.ontimeout = (timeout) => {
        if (window.ENABLE_DEBUG && typeof console.warn === 'function') {
          console.warn('XHR :: Browser timeout occurred: ' + JSON.stringify(timeout) + ', readyState: ' + xhr.readyState + ' ('+ xhrReadyStates[xhr.readyState] +') - path: ' + path);
        }
        handleTimeout();
    };

  // Enable this to get logging of the request urls that we invoke during GAPI use
    if (window.ENABLE_DEBUG && typeof console.debug === 'function') {
      console.debug('XHR :: xhr.open(' + path + ')');
    }

    xhr.open(method, path);
    xhr.setRequestHeader('Authorization', 'Bearer ' + access_token);

    if (typeof args.headers === 'object') {
        for (var headerName in args.headers) {
          if (headerName.toLowerCase() !== 'content-length') { // content-length is considered 'unsafe'
            xhr.setRequestHeader(headerName, args.headers[headerName]);
          }
        }
    }

    var clearResponseTimeout = function() {
      if (args.responseTimeout) {
        window.clearTimeout(args.responseTimeout);
        delete args.responseTimeout;
      }
    };

    var setResponseTimeout = function() {
      clearResponseTimeout();
      args.responseTimeout = window.setTimeout(handleTimeout, 5000);
    };
    var handleTimeout = function() {
      if (window.ENABLE_DEBUG && typeof console.log === 'function') {
          console.log('XHR :: Request timeout - aborting request - readyState: ' + xhr.readyState + ' ('+ xhrReadyStates[xhr.readyState] +') - path: ' + path);
      }
      clearResponseTimeout();
      xhr.abort();
      args.errorCallback("timeout");
    };

    xhr.onerror = function (event) {
      if (window.ENABLE_DEBUG && typeof console.debug === 'function') {
          console.debug('XHR :: Request error - readyState: ' + xhr.readyState + ' ('+ xhrReadyStates[xhr.readyState] + ') - aborting - path: ' + path, 'errorEvent: ', event);
      }
      clearResponseTimeout();
      args.errorCallback();
    };

    xhr.onabort = function (event) {
      if (window.ENABLE_DEBUG && typeof console.debug === 'function') {
          console.debug('XHR :: Request aborted - readyState: ' + xhr.readyState + ' ('+ xhrReadyStates[xhr.readyState] + ') - aborted - path: ' + path, 'abortEvent: ', event);
      }
    };

    var getHeadersAsObj = function (headersString) {
        if (headersString === null) return null;
        if (headersString === '') return '';

        var headers = {};
        var headerElements = headersString.split('\r\n');
        for (var i=0; i < headerElements.length ; i++) {
          var headerElement = headerElements[i];
          if (headerElement && headerElement.trim().length > 0) {
            var headerNameSeparatorIdx = headerElement.indexOf(':');
            var headerName = headerElement.substr(0, headerNameSeparatorIdx).trim();
            var headerValue = headerElement.substr(headerNameSeparatorIdx + 1).trim();
            if (!isNaN(parseFloat(headerValue)) && isFinite(headerValue)) {
              // If value is numeric, replace the value with the numeric representation
              headerValue = parseFloat(headerValue);
            }
            headers[headerName] = headerValue;
          }
        }
        return headers;
    };

    xhr.onload = function() {
      clearResponseTimeout();

      var rawResponseObject = {
        // TODO: body.
        // TODO: error handling; this function is called if a protocol-level error occurs (e.g. HTTP 404, 500)
        gapiRequest: {
          data: {
            status: this.status,
            statusText: this.statusText,
            responseHeaders: getHeadersAsObj(this.getAllResponseHeaders()),
          }
        }
      };

      if (this.response) {
        var respObject = this.response;
        if (typeof this.response === 'string') {
          var contentType = xhr.getResponseHeader('Content-Type');
          if (typeof contentType === 'string' && contentType.startsWith('application/json')) {
            try {
              respObject = JSON.parse(respObject);
            } catch (err) {
              var fallbackParsingSuccess = false;
              if (err instanceof SyntaxError) {
                try {
                  respObject = JSON.parse(respObject);
                } catch (err) {
                  try {
                    // Audio recorder bug could have been adding json files with base64 encoded content - handle this properly.
                    respObject = respObject.replace(/={1,2}$/, ''); // Strip base64 '='' padding at the end
                  } catch (decodeErr) {
                    if (console && console !== null && console.warn && console.warn !== null) {
                      console.warn('Failed parsing json string - raw response: ', this.response, 'Error: ', err, ' Callback will be called with the raw response');
                    }
                  }
                }
              }
            }
          }
        }
        args.callback(respObject, rawResponseObject);// Send the rawwResponseObject instead of the string
      } else {
        args.callback(null, rawResponseObject);// Send the rawwResponseObject instead of the string
      }
    };

    var progressObj = (method === 'PUT' || method === 'PATCH' ? xhr.upload : xhr);
    progressObj.onprogress = function (event) {
      // restart progress timeout for the next chunk of data
      if (window.ENABLE_DEBUG) {
          console.debug(`XHR :: onprogress: ${event.lengthComputable ? ((event.loaded/event.total)*100).toFixed(0) : '?'}% - ${method}: ${path} (${event.loaded}/${event.total} bytes)`);
      }
      setResponseTimeout();
    };

    // send the request
    if (typeof args.responseType === 'string') {
      xhr.responseType = args.responseType;
    }
    if (args.bodyType && args.bodyType !== null) {
      xhr.setRequestHeader('Content-Type', args.bodyType);
      xhr.send(args.body);
    } else if (typeof args.body !== 'undefined') {
      xhr.setRequestHeader('Content-Type', 'application/json');
      xhr.send(JSON.stringify(args.body));
    } else {
      xhr.send();
    }

    setResponseTimeout();
  };

  // Call client handler when gapi is ready.
  setTimeout(function () { gapiLoadedCallback(); }, 0);
}



