import BaseModule from 'components/BaseModule';
import MessageHandler from 'MessageHandler';

export default class PlatformInfo extends BaseModule {

    constructor(logger = console) {
        super();
    }

    requestSystemCpuInfo = () => {
        const dataURL = 'uniwebview://PlatformInfo?systemCpu=';
        if (chrome && chrome.system && chrome.system.cpu.getInfo && typeof chrome.system.cpu.getInfo === 'function') {
            chrome.system.cpu.getInfo((info) => {
                MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL + this.getObjAsBase64Json(info));
            });
        }
    };

    requestPlatformInfo = () => {

        const dataURL = 'uniwebview://PlatformInfo?platformInfo=';

        if (chrome && chrome.runtime && chrome.runtime.getPlatformInfo && typeof chrome.runtime.getPlatformInfo === 'function') {
            chrome.runtime.getPlatformInfo((platformInfo) => {
                MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL + this.getObjAsBase64Json({
                    nativePlatformInfo: platformInfo,
                    isArmArchitecture: platformInfo.arch.indexOf('arm') != -1,
                    is64bit: platformInfo.arch.indexOf('x86-64') != -1,
                    platformOS: platformInfo.os, /* "mac", "win", "android", "cros", "linux", or "openbsd" */
                    platformOSDetails: navigator.appVersion.match(/\(.*; ([^\)]+)\)/)[1],
                    chromeVersion: navigator.appVersion.match(/.*Chrome\/([0-9\.]+)/)[1]
                }));
            });
        } else {
            MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL + this.getObjAsBase64Json({
                    nativePlatformInfo: navigator.platform,
                    isArmArchitecture: this.isArmArchitecture(),
                    is64bit: this.is64BitArchitecture(),
                    platformOS: navigator.appVersion.match(/\(.*; ([^\)]+)\)/)[1],
                    platformOSDetails: navigator.appVersion.match(/\(.*; ([^\)]+)\)/)[1],
                    chromeVersion: navigator.appVersion.match(/.*Chrome\/([0-9\.]+)/)[1]
          }));
        }
    };

    requestSystemMemoryInfo = () => {
        const dataURL = 'uniwebview://PlatformInfo?systemMemory=';

        if (chrome && chrome.system && chrome.system.memory && typeof chrome.system.memory.getInfo === 'function') {
            chrome.system.memory.getInfo((info) => {
                MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL + this.getObjAsBase64Json(info));
            });
        }
    };

    requestLanguageInfo = () => {
        const dataURL = 'uniwebview://PlatformInfo?language=';

        if (chrome && chrome.i18n && typeof chrome.i18n.getAcceptLanguages === 'function') {
            chrome.i18n.getAcceptLanguages((languageList) => {
                var languages = languageList.join(",");
                var uiLanguage = (typeof chrome.i18n.getUILanguage === 'function' ? chrome.i18n.getUILanguage() : languageList[0]);
                console.log('Accept Languages: ' + languages + ', UI language: ' + uiLanguage);

                MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL + this.getObjAsBase64Json({
                    acceptLanguage: languageList[0],
                    acceptLanguages: languages,
                    uiLanguage: uiLanguage
                }));
            });

        }
    };

    getObjAsBase64Json(obj) {
        return window.btoa(window.unescape(window.encodeURIComponent(JSON.stringify(obj))));
    }

    getBitSystemArchitecture() {
        var _to_check = [] ;
        if ( window.navigator.cpuClass ) _to_check.push( ( window.navigator.cpuClass + "" ).toLowerCase() ) ;
        if ( window.navigator.platform ) _to_check.push( ( window.navigator.platform + "" ).toLowerCase() ) ;
        if ( navigator.userAgent ) _to_check.push( ( navigator.userAgent + "" ).toLowerCase() ) ;

        var _64bits_signatures = [ "x86_64", "x86-64", "Win64", "x64;", "amd64", "AMD64", "WOW64", "x64_64", "ia64", "sparc64", "ppc64", "IRIX64" ] ;
        var _bits = 32, _i, _c ;
        outer_loop:
                for( var _c = 0 ; _c < _to_check.length ; _c++ )
                {
                    for( _i = 0 ; _i < _64bits_signatures.length ; _i++ )
                    {
                        if ( _to_check[_c].indexOf( _64bits_signatures[_i].toLowerCase() ) != -1 )
                        {
                            _bits = 64 ;
                            break outer_loop;
                        }
                    }
                }
        return _bits ;
    }

    is32BitArchitecture() { return this.getBitSystemArchitecture() == 32 ? 1 : 0 ; }
    is64BitArchitecture() { return this.getBitSystemArchitecture() == 64 ? 1 : 0 ; }

    isArmArchitecture() {
        var _to_check = [] ;
        if ( window.navigator.cpuClass ) _to_check.push( ( window.navigator.cpuClass + "" ).toLowerCase() ) ;
        if ( window.navigator.platform ) _to_check.push( ( window.navigator.platform + "" ).toLowerCase() ) ;
        if ( navigator.userAgent ) _to_check.push( ( navigator.userAgent + "" ).toLowerCase() ) ;

        var _arm_signatures = [ "arm" ] ;
        var _arm = false, _i, _c ;
        outer_loop:
                for( var _c = 0 ; _c < _to_check.length ; _c++ )
                {
                    for( _i = 0 ; _i < _arm_signatures.length ; _i++ )
                    {
                        if ( _to_check[_c].indexOf( _arm_signatures[_i].toLowerCase() ) != -1 )
                        {
                            _arm = true ;
                            break outer_loop;
                        }
                    }
                }
        return _arm;
    }

}


