import ChromeApp from 'ChromeApp';
// import Logger from 'Logger';
import Splashscreen from 'components/Splashscreen';
import WindowController from 'components/Window';
import Storage from 'components/Storage';
import PlatformInfo from 'components/PlatformInfo';
import Video from 'components/Video';
import Audio from 'components/Audio';
import Bluetooth from 'components/Bluetooth';
import ContentViewerWebview from 'components/ContentViewerWebview';
import SyncFS from 'components/SyncFS';
import ExternalLink from 'components/ExternalLink';
import Killswitch from 'components/Killswitch';
import OSLimiter from 'components/OSLimiter';
//import UpdateChecker from 'components/UpdateChecker';

// Timestamps in console - start
if (!Date.now) {
  Date.now = function() {
    return new Date().getTime();
  };
}
var startTime = Date.now();
function tstamp() {
  var t = (Date.now() - startTime) / 1000.0;
  return '[' + t + '] ';
}

function getArgString(args) {
  if (typeof args === 'string') {
    return args;
  }
  // return JSON.stringify(args);
  return concatArgs(args);
}

/**
 * @ignore
 * Get a message to print by the logger
 *
 * @param {object|Array} args - The arguments to log
 * @param {number} [offset = 0] - The number of arguments to skip - by default no arguments are skipped
 * @param {string} [separator = ' '] The separator to use between the args - by default a space is used
 * @return {string}
 */
function concatArgs(args, offset = 0, separator = ' ') {
  var message = '';
  for (var i = offset; i < args.length; i++) {
    if (i > offset) {
      message += separator;
    }
    var type = typeof args[i];
    if (args[i] !== null) {
      if (type === 'object') {
        var o = {};
        o.o = args[i];
        var cache = [];
        if (
          args[i].hasOwnProperty('toString') &&
          typeof args[i].toString === 'function'
        ) {
          message += args[i].toString();
        } else {
          // message += JSON.stringify(args[i]);
          message += JSON.stringify(
            o,
            function(key, value) {
              if (typeof value === 'object' && value !== null) {
                if (cache.indexOf(value) !== -1) {
                  // Circular reference found, ignore key
                  return;
                }
                // Store value in our collection
                cache.push(value);
              }
              return value;
            },
            '\t'
          );
        }
        cache = null;
      } else {
        message += args[i];
      }
    } else {
      message += 'null';
    }
  }

  return message;
}

var oldConsole = console;
window.console = {
  log: function() {
    var args = [getArgString(arguments)];
    Array.prototype.unshift.call(args, tstamp());
    oldConsole.log.apply(oldConsole, args);
  },
  trace: function() {
    var args = [getArgString(arguments)];
    Array.prototype.unshift.call(args, tstamp());
    oldConsole.trace.apply(oldConsole, args);
  },
  info: function() {
    var args = [getArgString(arguments)];
    Array.prototype.unshift.call(args, tstamp());
    oldConsole.info.apply(oldConsole, args);
  },
  error: function() {
    var args = [getArgString(arguments)];
    Array.prototype.unshift.call(args, tstamp());
    oldConsole.error.apply(oldConsole, args);
  },
  debug: function() {
      const argString = getArgString(arguments);
      if (argString.indexOf('Screen position out of view frustum') !== -1) {
        return;
      }
      var args = [argString];
    Array.prototype.unshift.call(args, tstamp());
    oldConsole.debug.apply(oldConsole, args);
  },
  assert: function() {
    var args = [getArgString(arguments)];
    Array.prototype.unshift.call(args, tstamp());
    oldConsole.assert.apply(oldConsole, args);
  },
  warn: function() {
    var args = [getArgString(arguments)];
    Array.prototype.unshift.call(args, tstamp());
    oldConsole.warn.apply(oldConsole, args);
  },
  time: function() {
    var args = [getArgString(arguments)];
    oldConsole.time.apply(oldConsole, args);
  },
  timeStamp: function() {
    var args = [getArgString(arguments)];
    oldConsole.timeStamp.apply(oldConsole, args);
  },
  timeEnd: function() {
    var args = [getArgString(arguments)];
    oldConsole.timeEnd.apply(oldConsole, args);
  }
};
// Timestamps in console - end

const $logger = console;

const storage = new Storage();

const windowController = new WindowController();

const chromeApp = new ChromeApp($logger, window);

var syncFSLogger = {
  log: function(...args) {
    $logger.log('[SyncFS] ', args);
  }
};

const splashscreen = new Splashscreen();
window.addEventListener('load', () => {
  splashscreen.onBoundsChanged();
});

const osLimiter = new OSLimiter(['cros'], true, 'splashscreen');

//const updateChecker = new UpdateChecker($logger);

var initApp = function() {
  $logger.debug('[App.js] On supported platform');

  // Create the webview to hold the webGL
  //<webview id="webgl" src="webgl/index.html" partition="persist:webgl"></webview>
  var webGLwebview = document.createElement('webview');
  webGLwebview.id = 'webgl';
  webGLwebview.setAttribute('src', 'webgl/index.html');
  webGLwebview.setAttribute('partition', 'persist:webgl');

  // Append webview to parent element
  document.getElementById('parent').appendChild(webGLwebview);

  setTimeout(() => {
    document.getElementById('webgl').setZoomMode('disabled');
  }, 1000);

  document.getElementById('webgl').addEventListener('dialog', function(e) {
    $logger.debug('[App.js] Dialog from webgl webview', e.messageText);
  });

  document.addEventListener('mousewheel', function(e) {
    if (e.ctrlKey === true) {
      e.preventDefault();
      e.stopPropagation();
    }
  });

  chromeApp
    .initialize()
    .then(() => {
      chrome.runtime.getPlatformInfo(platformInfo => {
        $logger.debug(
          chrome.runtime.getManifest().name +
            ' manifest version: ' +
            chrome.runtime.getManifest().version +
            ' starting on ' +
            platformInfo.os +
            ' ' +
            platformInfo.arch +
            ' - Chrome details(' +
            navigator.appVersion +
            ') - onLine: ' +
            navigator.onLine
        );
      });

      windowController.initialize(storage);

      chromeApp.register('Killswitch', new Killswitch());
      chromeApp.register('ExternalLink', new ExternalLink());
      chromeApp.register('Splashscreen', splashscreen);
      chromeApp.register('Storage', storage);
      chromeApp.register('PlatformInfo', new PlatformInfo());
      chromeApp.register('Audio', new Audio());
      chromeApp.register('Video', new Video(storage));
      chromeApp.register(
        'Content',
        new ContentViewerWebview({
          id: 'contentViewer',
          style: {
            width: '600px',
            position: 'absolute',
            display: 'none'
          },
          partition: 'persist:webgl'
        })
      );
      chromeApp.register('Bluetooth', new Bluetooth($logger, window));
      chromeApp.register('SyncFS', new SyncFS(syncFSLogger, splashscreen));
    })
    // .then( () => {
    // setTimeout(() => {
    //     updateChecker.checkForUpdates();
    // }, 15000); // set some delay so we minimize the chance of interfering with the normal startup flow
    // })
    .catch(err => {
      console.error('ChromeApp failed registration of libraries', err);
    });
};

// Check if OS is supported
osLimiter.runOnApprovedOS(initApp);



