import BaseModule from 'components/BaseModule';
import MessageHandler from 'MessageHandler';

export default class VideoController extends BaseModule {
    constructor($storage) {
        super();
        this.storage = $storage;
        this.videoStream = null;
        this.videoElement = null;
        this.videoContainerElement = null;
        this.snapshotCanvas = null;
        this.snapshotCanvasContext = null;
        this.videoRecordRTC = null;
        this.previewFilePath = null;

        this.previewImageExt = 'jpg';
        this.previewVideoExt = 'webm';

        this.livePreviewPosition = {};

        window.requestAnimationFrame = window.requestAnimationFrame ||
            window.webkitRequestAnimationFrame;

        navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia;
    }

    playVideo = (url, bgColor) => {
        const videoContainer = document.createElement('div');
        videoContainer.style.backgroundColor = bgColor;
        videoContainer.style.position = 'fixed';
        videoContainer.style.top = '0';
        videoContainer.style.left = '0';
        videoContainer.style.width = '100%';
        videoContainer.style.height = '100%';

        const video = document.createElement('video');
        video.className = 'lobbyvideo'
        video.src = url;
        video.preload = 'auto';
        video.autoplay = true;
        video.controls = true;
        video.onended = () => {
            document.body.removeChild(videoContainer);
        };
        video.onclick = () => {
            document.body.removeChild(videoContainer);
        };
        videoContainer.appendChild(video);
        document.body.appendChild(videoContainer);
    };

    setupVideoElements = () => {
        if (!this.videoContainerElement) {
            this.videoContainerElement = document.createElement('div');
            document.body.appendChild(this.videoContainerElement);

            this.videoElement = document.createElement('video');
            this.videoElement.autoplay = true;
            this.videoContainerElement.appendChild(this.videoElement);
            this.videoElement.style.position = 'relative';
            this.videoElement.style.marginLeft = 'auto';
            this.videoElement.style.marginRight = 'auto';
            this.videoElement.style.top = '50%';
            this.videoElement.style.transform = 'translateY(-50%)';
            this.videoElement.style.display = 'block';
            this.videoElement.style.width = '100%';
            this.videoElement.style.height = '100%';

            this.videoElement.volume = 0;

            this.videoElement.srcObject = this.videoStream;
        }

        if (!this.snapshotCanvas) {
            this.snapshotCanvas = document.createElement('canvas');
            this.snapshotCanvas.style.display = 'none';
            this.snapshotCanvas.style.width = '100%';
            this.snapshotCanvas.style.height = '100%';
            this.snapshotCanvasContext = this.snapshotCanvas.getContext('2d');
            document.body.appendChild(this.snapshotCanvas);
        }

        if (!this.videoRecordRTC) {
            this.videoRecordRTC = new RecordRTC(this.videoStream, {type: 'video'});
            this.videoRecordRTC.mimeType = {
                video: 'video/webm',
                audio: 'audio/wav',
            };

            this.videoRecordRTC.onStateChanged.call = (state) => {
                console.log('Video :: Video Recorder state changed to: ' + state['state']);
            };

        } else {
            console.debug('Video :: setupVideoElements - videoRecordRTC already set - resetting it');
            this.videoRecordRTC.reset();
        }

        this.updateLivePreviewPosition();
    };

    teardownVideoElements = () => {
        if (this.videoContainerElement) {
            if (this.videoElement) {
                URL.revokeObjectURL(this.videoElement.src);
            }
            document.body.removeChild(this.videoContainerElement);
            this.videoElement = null;
            this.videoContainerElement = null;
        }

        if (this.snapshotCanvas) {
            document.body.removeChild(this.snapshotCanvas);
            this.snapshotCanvasContext = null;
            this.snapshotCanvas = null;
        }

        if (this.videoStream) {
            this.videoStream.stop();
            this.videoStream = null;
        }

        if (this.videoRecordRTC) {
            this.videoRecordRTC.reset();
            this.videoRecordRTC = null;
        }
    };

    updateLivePreviewPosition = () => {
        if (this.videoContainerElement) {
            this.videoContainerElement.style.position = this.livePreviewPosition.position;
            this.videoContainerElement.style.top = this.livePreviewPosition.top;
            this.videoContainerElement.style.left = this.livePreviewPosition.left;
            this.videoContainerElement.style.width = this.livePreviewPosition.widthPX;
            this.videoContainerElement.style.height = this.livePreviewPosition.heightPX;
        }

        if (this.snapshotCanvas) {
            this.snapshotCanvas.width = this.livePreviewPosition.width;
            this.snapshotCanvas.height = this.livePreviewPosition.height;
        }
    };

    startCaptureSession = () => {
        if (!this.videoContainerElement) {
            let width = this.livePreviewPosition.width;
            width /= 10.0;
            width = Math.floor(width) * 10;

            let height = this.livePreviewPosition.height;
            height /= 10.0;
            height = Math.floor(height) * 10;

            console.log('Video :: startCaptureSession - this.livePreviewPosition.width:  ' + this.livePreviewPosition.width + ' --> ' + width + ', this.livePreviewPosition.height: ' + this.livePreviewPosition.height + ' --> ' + height);
            navigator.getUserMedia(
                {
                    video: {
                        width: { /*min: width,*/ ideal: width/*, max: width */},
                        height: { /*min: height, */ideal: height/*, max: height*/ },
                    },
                    audio: true,
                }, (videoStream) => {
                    this.videoStream = videoStream;
                    this.setupVideoElements();
                }, (e) => {
                    console.error('Video :: startCaptureSession - failed getting videostream, tearing down video elements', e);
                    this.teardownVideoElements();
                });
        }
        else {
            this.videoElement.style.display = 'block';
        }
    };

    setLivePreviewPosition = (x, y, width, height) => {
        if (this.livePreviewPosition) {
            this.livePreviewPosition.position = 'fixed';
            this.livePreviewPosition.top = `${y}px`;
            this.livePreviewPosition.left = `${x}px`;
            this.livePreviewPosition.widthPX = `${width}px`;
            this.livePreviewPosition.heightPX = `${height}px`;
            this.livePreviewPosition.width = width;
            this.livePreviewPosition.height = height;
        }

        this.updateLivePreviewPosition();
    };

    startRecording = () => {
        if (!this.videoRecordRTC) {
            console.debug('Video :: startRecording - videoRecordRTC is not set, invoking setupVideoElements');
            this.setupVideoElements();
        }
        if (this.videoRecordRTC) {
            this.videoRecordRTC.startRecording();
        } else {
            console.info('Video :: Unable to start recording as no recordRTC instance available');
        }
    };

    stopRecording = (filePath) => {
        this.previewFilePath = filePath;
        if (this.videoRecordRTC) {
            this.videoRecordRTC.stopRecording(() => {
                this.handleVideo(this.videoRecordRTC.toURL(), this.videoRecordRTC.getBlob())
            });
        } else {
            console.info('Video :: stopRecording - No videoRecordRTC instance - Skipping stopRecording call');
        }
    };

    handleVideo = (videoURL, videoBlob) => {
        const width = this.videoContainerElement.style.width.replace('px', '');
        const height = this.videoContainerElement.style.height.replace('px', '');
        this.snapshotCanvasContext.drawImage(this.videoElement, 0, 0, width, height);
        const dataString = this.snapshotCanvas.toDataURL('image/jpeg', 1);
        const imageBlob = this.dataURItoBlob(dataString);

        this.storage.saveFile(videoBlob, this.previewVideoExt, () => {
            this.saveLocalPreviewFile(videoBlob, this.previewVideoExt, 'StopVideoRecording?VideoFilePath=');
            this.saveLocalPreviewFile(imageBlob, this.previewImageExt, 'CaptureImage?ImageFilePath=');
        }, (error) => {
            console.log(`Video :: handleVideo - Unable to save file: ${error}`);
            const dataURL = 'uniwebview://StopVideoRecording?VideoRecordingCancelled=true';
            MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL)
        });
    };

    stopCaptureSession = (currentlyCapturing) => {
        if (currentlyCapturing === 'True') {
            this.videoElement.style.display = 'none';
        } else {
            if (this.videoRecordRTC) {
                this.videoRecordRTC.stopRecording((videoURL) => {
                    console.debug('Video :: stopCaptureSession - stopRecording completed');
                });
            }

            try {
                this.teardownVideoElements();
            } catch (err) {
                console.error('Video :: stopCaptureSession: Failed teardownVideoElements', err);
            }

        }
    };

    captureImage = (filePath) => {
        this.previewFilePath = filePath;
        const width = this.videoContainerElement.style.width.replace('px', '');
        const height = this.videoContainerElement.style.height.replace('px', '');
        this.snapshotCanvasContext.drawImage(this.videoElement, 0, 0, width, height);
        const dataString = this.snapshotCanvas.toDataURL('image/jpeg', 1);

        const imageBlob = this.dataURItoBlob(dataString);
        this.storage.saveFile(imageBlob, this.previewImageExt, () => {
            this.saveLocalPreviewFile(imageBlob, this.previewImageExt, 'CaptureImage?ImageFilePath=');
        }, (error) => {
            console.log(`Video :: Unable to save captured image file: ${error}`);
            const dataURL = 'uniwebview://CaptureImage?CaptureImageCancelled=true';
            MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL);
        });
    };

    captureCanvasImage = (imageData, filePath) => {
        this.previewFilePath = filePath;
        const imageBlob = this.dataURItoBlob(imageData);
        this.storage.saveFile(imageBlob, this.previewImageExt, () => {
            this.saveLocalPreviewFile(imageBlob, this.previewImageExt, 'CaptureImage?ImageFilePath=');
        }, (error) => {
            console.log(`Video :: Unable to save captured canvas image file: ${error}`);
            const dataURL = 'uniwebview://CaptureImage?CaptureImageCancelled=true';
            MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL);
        });
    };

    saveLocalPreviewFile = (fileBlob, extension, dataURL) => {
        const filePath = `${this.previewFilePath}.${extension}`;
        const newDataURL = `uniwebview://${dataURL}${filePath}`;
        console.log(`Video :: Saving file to index db filepath: ${filePath} - newDataUrl: ${newDataURL}`);
        this.storage.saveFileToIndexDB(filePath, fileBlob, newDataURL);
    };

    dataURItoBlob = (dataURI) => {
        const data = atob(dataURI.split(',')[1]);
        const mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];
        const array = new Uint8Array(data.length);
        for (let i = 0; i < data.length; i++) {
            array[i] = data.charCodeAt(i);
        }
        return new Blob([array], {type: mimeString});
    };
}



