let instance = null;

export default class MessageForwarder {
  constructor() {
    if (!instance) {
      instance = this;
    }
    return instance;
  }

  static setAppWindow(appWindow) {
    instance.appWindow = appWindow;
  }

  static setAppOrigin(appOrigin) {
    instance.appOrigin = appOrigin;
  }

  static getAppWindow() {
    return instance.appWindow;
  }

  static getAppOrigin() {
    return instance.appOrigin;
  }

  handle(...args) {
    MessageForwarder.forward(args.target, args.method, args.data);
  }

  static forward(method, target, data) {
    if (MessageForwarder.getAppOrigin() && MessageForwarder.getAppWindow()) {
      MessageForwarder.getAppWindow().postMessage({
        sender: 'unity',
        target,
        method,
        data,
      }, MessageForwarder.getAppOrigin());
    }
  }
}



