import BaseModule from 'components/BaseModule';
import MessageHandler from 'MessageHandler';

export default class Audio extends BaseModule {

    constructor() {
        super();

        this.audioContext = null;

        this.recorder = null;
        this.isAudioRecording = false;
        this.isListening = false;
        this.enabled = false;
        this.audioBufferSize = 0;
        this.audioWorker = new Worker('js/audioWorker.js');
        this.audioWorker.postMessage({
            command: 'init',
            config: {},
        });
        this.audioRecordRTC = null;
        this.audioStream = null;

        this.recorder = null;
        this.inputPoint = null;
        this.realAudioInput = null;
        this.zeroGain = null;
        this.audioIntervalID = -1;
    }

    initAudio = (bufferSize) => {
        this.audioBufferSize = bufferSize;
        navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia;

        window.AudioContext = window.AudioContext || window.webkitAudioContext;

        this.audioContext = new AudioContext();
    };

    enableDisableAudio = (enable) => {
        this.enabled = (enable === 'True');

        if (this.enabled) {
            navigator.getUserMedia({
                audio: true,
            }, (audioStream) => {
                this.audioStream = audioStream;
                this.setupVolumeListening();
                this.audioIntervalID = setInterval(() => { this.getVolume(); }, 1000);
            }, (error) => {
                console.log(`The following error occured: ${error}`);
            });
        }
        else {

            this.teardownAudioRecording();
            this.teardownVolumeListening();

            if (this.audioStream) {
                this.audioStream.stop();
                this.audioStream = null;
            }
            clearInterval(this.audioIntervalID);
        }
    };

    startRecording = () => {

        this.isListening = false;
        this.teardownVolumeListening();

        this.setupAudioRecording();
        if (this.audioRecordRTC) {
            // this.isAudioRecording = true;
            this.audioRecordRTC.startRecording();
        } else {
            console.info('startRecording - No audioRecordRTC instance - Skipping startRecording call');
        }

    };

    stopRecording = (resumeListening) => {
        if (this.isAudioRecording && this.enabled) {
            if (this.audioRecordRTC) {
                this.audioRecordRTC.stopRecording(() => {
                    this.handleAudio(this.audioRecordRTC.toURL());
                });
            } else {
                console.info('stopRecording - No audioRecordRTC instance - Skipping stopRecording call');
            }

        }

        const newIsListening = resumeListening;
        if (this.isListening != newIsListening && this.enabled) {
            if (newIsListening) {
                this.setupVolumeListening();
            }
            else {
                this.teardownVolumeListening();
            }
        }
        this.isListening = newIsListening;
    };

    handleAudio = (audioURL) => {
        if (this.audioRecordRTC) {
            let dataURL = `uniwebview://AudioRecorded?AudioDataURL=${audioURL}`;
            MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL);
        } else {
            console.debug('Audio :: handleAudio - this.audioRecordRTC is null or undefined');
        }
        this.teardownAudioRecording();
    };

    setupAudioRecording = () => {
        this.audioRecordRTC = new RecordRTC(this.audioStream, {
            recorderType: StereoAudioRecorder,
            type: 'audio',
            numberOfAudioChannels: 1,
            mimeType: 'audio/wav',
            bufferSize: 0,
            disableLogs: false
        });
        this.audioRecordRTC.onStateChanged.call = (state) => {
            console.log('Audio :: Audio Recorder state changed: ', state);
            this.isAudioRecording = (state['state'] === 'recording');
        };
    };

    teardownAudioRecording = () => {
        this.audioRecordRTC = null;
    };

    setupVolumeListening = () => {
        if (this.audioStream) {
            this.audioWorker.onmessage = (e) => {
                switch (e.data.command) {
                    case 'volume':
                        this.sendVolume(e.data.volume);
                        break;
                    default:
                        console.log('AudioController Unknow message');
                        break;
                }
            };

            this.recorder = this.audioContext.createScriptProcessor(this.audioBufferSize, 1, 1);
            this.recorder.onaudioprocess = (e) => {
                if (!this.isListening || !this.enabled) {
                    return;
                }

                const inputData = e.inputBuffer.getChannelData(0);

                this.audioWorker.postMessage({
                    command: 'record',
                    buffer: [inputData],
                });
            };

            this.inputPoint = this.audioContext.createGain();
            this.inputPoint.gain.setTargetAtTime(1, this.audioContext.currentTime, 0.015);

            // Create an AudioNode from the stream.
            this.realAudioInput = this.audioContext.createMediaStreamSource(this.audioStream);
            this.realAudioInput.connect(this.inputPoint);


            this.zeroGain = this.audioContext.createGain();
            this.zeroGain.gain.setTargetAtTime(0.0, this.audioContext.currentTime, 0.015);
            this.inputPoint.connect(this.zeroGain);
            this.zeroGain.connect(this.audioContext.destination);

            this.inputPoint.connect(this.recorder);
            this.recorder.connect(this.audioContext.destination);
        }
    };

    teardownVolumeListening = () => {
        if (this.recorder) {
            this.recorder.onaudioprocess = null;
            this.recorder = null;
        }

        if (this.realAudioInput) {
            this.realAudioInput.disconnect();
            this.realAudioInput = null;
        }

        if (this.inputPoint) {
            this.inputPoint.disconnect();
            this.inputPoint = null;
        }

        if (this.zeroGain) {
            this.zeroGain.disconnect();
            this.zeroGain = null;
        }
    };

    getVolume = () => {
        if (this.isListening) {
            this.audioWorker.postMessage(
                {
                    command: 'getVolume',
                }
            );
        }
    };

    sendVolume = (clipVolume) => {
        const dataURL = `uniwebview://AudioVolume?AudioVolumeValue=${clipVolume}`;
        MessageHandler.callUnity(null, 'OnRecieveMessageFromChromeOS', dataURL);
    };
}



