import Webview from 'components/Webview';
import MessageHandler from 'MessageHandler';

import Storage from 'components/Storage';

export default class ContentViewerWebview extends Webview {
  constructor(options) {
    super(options, false);
    this.eventHandlers['contentload'] = this.event_contentload.bind(this);

    this.unityMethodToInvoke = 'OnRecieveMessageFromChromeOS';

    this.setup(options);
    setTimeout(() => {
      this.webview.setZoomMode('disabled');
    }, 20000);
    this.storage = new Storage();

    this.webview.addEventListener('permissionrequest', function(e) {
      if (e.permission === 'fullscreen') {
        e.request.allow();
      }
    });
  }

  event_contentload() {
    // Welcome message
    this.sendMessage({
      sender: 'hello'
    });

    MessageHandler.callUnity('content', this.unityMethodToInvoke, '');
  }

  setTransparentBackground = enabled => {
    this.webview.style.height = enabled === 'True' ? '400px' : '200px';
  };

  setInsets = (top, left, bottom, right, width, height) => {
    this.webview.style.top = `${top}px`;
    this.webview.style.left = `${left}px`;
    this.webview.style.bottom = `${bottom}px`;
    this.webview.style.right = `${right}px`;
    this.webview.style.width = `unset`;
    this.webview.style.height = `unset`;
  };

  setVisible = visible => {
    this.webview.style.display = visible === 'True' ? 'inherit' : 'none';
  };

  invokeUnity = data => {
    MessageHandler.callUnity('content', this.unityMethodToInvoke, data);
  };

  handleExportImage = dataURI => {
    this.handleExport(dataURI, 'image/jpg', 'jpg');
  };

  handleExportPdf = dataURI => {
    this.handleExport(dataURI, 'application/pdf', 'pdf');
  };

  handleExport(dataURI, mimeType, extension) {
    const splitString = dataURI.split(';base64,');
    const dataBase64 = splitString.length > 1 ? splitString[1] : splitString[0];

    this.storage.saveFile(
      this.b64toBlob(dataBase64, mimeType),
      extension,
      () => {
        this.evaluatingJavaScript('ExportDone()');
      },
      err => {
        this.evaluatingJavaScript('ExportDone()');
      }
    );
  }

  handleSaveSDT = data => {
    MessageHandler.callUnity('content', 'OnEvalJavaScriptFinished', data);
  };

  b64toBlob(b64Data, contentType = '', sliceSize = 512) {
    const byteCharacters = atob(b64Data);
    const byteArrays = [];

    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      const slice = byteCharacters.slice(offset, offset + sliceSize);

      const byteNumbers = new Array(slice.length);
      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      const byteArray = new Uint8Array(byteNumbers);

      byteArrays.push(byteArray);
    }

    const blob = new Blob(byteArrays, { type: contentType });
    return blob;
  }
}



