let instance = null;

export default class MessageHandler {

  constructor() {
    if (!instance) {
      instance = this;
    }
    return instance;
  }

  static getInstance() {
    if (!instance) {
      instance = new MessageHandler();
    }

    return instance;
  }

  static setWebGL(view) {
    MessageHandler.getInstance().view = view;
  }

  static callWebGL(target, method, data) {
    MessageHandler.getInstance().view.contentWindow.postMessage({
      target,
      method,
      data
    }, '*');
  }

  // NOTE(Catalin,22.03.2016): sender is currently ignored past this call.
  static callUnity(sender, method, data) {
    MessageHandler.getInstance().view.contentWindow.postMessage({
      sender,
      target: 'unity',
      method,
      data,
    }, '*');
  }

  // Data gets stored into a shared memory arena between unity and js.
  // Do the equivalent of callUnity(nil, unityCallback.method, metadata) after the data is ready.
  static sendDataToUnity(data, metadata, transferID, unityCallback) {
    MessageHandler.getInstance().view.contentWindow.postMessage({
      target: "sendUnityData",
      data: data,
      metadata: metadata,
      transferID: transferID,
      unityCallback: unityCallback ? unityCallback : "JSArrayBufferReady",
    }, '*');
  }

  // UnityApp will call sender[callbackName](metadata, data);
  static requestDataFromUnity(transferID, sender, callbackName, metadata) {
    MessageHandler.getInstance().view.contentWindow.postMessage({
      target: "requestUnityData",
      transferID: transferID,
      sender: sender,
      metadata: metadata,
      callbackName: callbackName,
    }, '*');
  }
}



