import BaseModule from 'components/BaseModule';
import SyncError from 'components/SyncError';
import { configureGoogleAPI } from 'gapi_chrome_apps';
import { StringView } from 'stringview';

/* Google Drive API documentation specifies that the value
   should be a multiple of 256KB */
window.GD_RESUMABLE_UPLOAD_CHUNK_SIZE = 2 * 256 * 1024;

export default class GoogleDrive extends BaseModule {

    constructor(timestampFunc) {
        super();
        this.hasUserDeclinedAuth = false;
        this.access_token = undefined;
        this.pendingRequests = {};
        this.reqIdx = 0;
        this.maxAttempts = 5;
        const maxRequestsPerSecond = 10;
        // slightly increase the interval to decrease the chance of actually making too many requests per second
        this.requestInterval = Math.ceil(1.1 * (1000 / maxRequestsPerSecond));
        this.requestQueue = [];
        this._syncfsTimestamp = timestampFunc;
    }

    initialize = (SyncFS) => {
        var _this = this;
        _this.SyncFS = SyncFS;
        return new Promise((resolve, reject) => {
            console.log('loadGoogleAPI');
            configureGoogleAPI(function() {
                console.log('Google API loaded');
                _this._authenticate(resolve, reject);
            });
        });
    };

    _authenticate(resolve, reject) {
        var _this = this;
        var success = function(token) {
            _this.access_token = token;
            _this.hasUserDeclinedAuth = false;
            resolve();
            _this.startPerformingRequests();
        };
        var failure = function(error) {
            _this.access_token = undefined;
            // NOTE: Ask google about this. There's no guarantee this message is not localized.
            // Ok, we asked google, they said it's not localized, but the guy who answered was not 100% sure.
            _this.hasUserDeclinedAuth = (error.message === "The user did not approve access.");
            reject(new SyncError(error.message, error.code, error.reason));
            _this.startPerformingRequests();
        };
        // try to use the cached auth. token first
        gapi.auth.authorize({immediate: true}, function(result) {
            console.log('Received authorization result (immediate = true)', result);
            if (!result.error) {
                success(result.access_token);
            } else {
                // show the GD authorization popup to the user
                _this._checkIfURLReachable("https://www.googleapis.com/drive/v3/about").then(() => {
                    gapi.auth.authorize({immediate: false}, function(result) {
                        console.log('Received authorization result (immediate = false)', result);
                        if (!result.error) {
                            success(result.access_token);
                        } else {
                            failure(result.error);
                        }
                    });
                }).catch((e) => {
                    console.log("Google Drive not reachable.", e);
                    failure("Google Drive not reachable");
                });
            }
        });
    }

    _checkIfURLReachable = (url) => {
        return new Promise((resolve, reject) => {
            var xhr = new XMLHttpRequest();
            xhr.open("GET", url);
            xhr.timeout = 10000;
            xhr.onload = function() {
                console.log("_checkIfURLReachable: onload", xhr);
                resolve();
            };
            xhr.onerror = function() {
                console.log("_checkIfURLReachable: onerror", xhr);
                reject();
            };
            xhr.ontimeout = function() {
                console.log("_checkIfURLReachable: ontimeout", xhr);
                reject();
            };
            xhr.send();
        });
    };

    revokeCredentials() {
        // do not stop performing requests - we need to make the next request and try reauthenticating if it ends with 401
        //this.stopPerformingRequests();
        if (this.access_token) {
            chrome.identity.removeCachedAuthToken({token: this.access_token});
            this.access_token = undefined;
        }
    }

    reauthenticate = () => {
        return new Promise((resolve, reject) => {
            this.revokeCredentials();
            this._authenticate(resolve, reject);
        });
    };

    attemptGoogleReconnect() {
        if (this.isAttemptingReconnect) return;
        var _this = this;
        _this.isAttemptingReconnect = true;
        var reconnectAttempt = 0;

        // Set up some bogus but legit parameters that google can actually respond to.
        var rArgs = {
            path: "/drive/v3/files",
            params: {
                "q": "",
                "pageSize": 2,
                "spaces": "appDataFolder",
            }
        };

        var doRequest = function() {
            gapi.client.request(rArgs);
        };

        var retryRequest = function() {
            var to = 1000 * reconnectAttempt * reconnectAttempt;
            console.log('retryRequest executing - to: ' + to + ', reconnectAttempt: ' + reconnectAttempt + ', rArgs: ' + JSON.stringify(rArgs));
            if (to > 10 * 60 * 1000) {
                to = 10 * 60 * 1000;
            } // max 10 minutes interval;
            reconnectAttempt++;
            window.setTimeout(doRequest, to);
        };

        var reconnectSuccessful = function() {
            _this.SyncFS.solveStatusReason("gdrive_offline");
            _this.isAttemptingReconnect = false;
        };

        rArgs.callback = function(response, rawResponse) {
            if (typeof rawResponse === 'string') {
                rawResponse = JSON.parse(rawResponse);
            }
            var rawResponseSuccess = (
                    rawResponse
                    && rawResponse.gapiRequest
                    && rawResponse.gapiRequest.data
                    && rawResponse.gapiRequest.data.status
                    && rawResponse.gapiRequest.data.status === 200) || false;
            var hasStatus = (
                    rawResponse
                    && rawResponse.gapiRequest
                    && rawResponse.gapiRequest.data
                    && rawResponse.gapiRequest.data.responseHeaders
                    && rawResponse.gapiRequest.data.responseHeaders.status) || false;
            var status = hasStatus ? rawResponse.gapiRequest.data.responseHeaders.status : -1;

            if (rawResponseSuccess || status === 200 || status === 401 || response === null || response.error === null ||
                    (response.error && response.error.code === 401)
            ) { // Don't worry about not being authenticated, that gets handled somewhere else. Here we just try to regain a connection to google.
                // console.log('Google Drive :: Callback for doRequest ==> reconnectSuccessful');
                reconnectSuccessful();
            } else {
                // console.log('Google Drive :: Callback for doRequest ==> retryRequest -[rawResponseSuccess: ' + rawResponseSuccess +']', rawResponse);
                retryRequest();
            }
        };
        rArgs.errorCallback = retryRequest;

        doRequest();
    }

    handleGoogleError(requestIdx, syncError) {
        var _this = this;
        var req = _this.pendingRequests[requestIdx];

        var retryRequest = () => {
            _this.enqueueRequest(req.args);
        };

        // There's a likely bug in our code somewhere where we create the calls to google apis if we got here.
        var failWithThrow = function(throwMessage) {
            req.reject(syncError);
            throw new Error(throwMessage);
        };

        // The request failed for external reasons and we could not sync. Start pinging google so we know when it's back online.
        var failSimple = function(newError) {
            req.reject(newError ? newError : syncError);
            delete _this.pendingRequests[requestIdx];

            if (newError === null || newError.message !== "The user did not approve access.") {
                _this.attemptGoogleReconnect();
            }
        };

        var immediateReattempt = function() {
            retryRequest();
        };

        var tryReatempt = function() {
            if (req.attempt < _this.maxAttempts) {
                req.attempt++;
                // Reattempt the request after 0.5, 2, 4.5, 8 and 12.5 seconds.
                window.setTimeout(retryRequest, 500 * req.attempt * req.attempt);
            } else {
                failSimple(new SyncError("timeout", syncError.code));
            }
        };

        switch (syncError.code) {
            // 400: Bad Request
            //      --- Should never happen for us, this means there's a bug in our code, fix it!
            case 400: {
                failWithThrow('Bad request! ' + req.args.path);
            } break;

            // 401: Invalid Credentials
            //      --- Try re-authentication. If it fails then users should not be able to use the app?
            case 401: {
                if (_this.hasUserDeclinedAuth) {
                    failSimple();
                } else {
                    _this.reauthenticate().then(() => {
                        immediateReattempt();
                    }).catch((syncError) => {
                        failSimple(syncError);
                    });
                }
            } break;

            // 403: Daily Limit Exceeded
            //      --- Solution involves politics, buy additional quota.
            // 403: User Rate Limit Exceeded
            // 403: Rate Limit Exceeded (these 2 are caused by doing too many requests in a short amount of time)
            //      --- Raise per-user quota
            //      --- Batch requests
            //      --- Exponential backoff
            // 403: The user has not granted the app {appId} {verb} access to the file {fileId}
            // 403: The user does not have sufficient permissions for file {fileId}
            // 403: App with id {appId} cannot be used within the authenticated user's domain
            //      --- Should never happen for us. It means we're trying to access a file outside
            //          of appData, that is a bug.
            case 403: {
                var reason = syncError.reason;
                if (reason === "dailyLimitExceeded") {
                    // Buy more quota!
                } else if (reason === "userRateLimitExceeded") {
                    tryReatempt();
                } else if (reason === "rateLimitExceeded") {
                    tryReatempt();
                } else if (reason === "appNotAuthorizedToFile") {
                    failWithThrow('No access to file! ' + req.args.path);
                } else if (reason === "insufficientFilePermissions") {
                    failWithThrow('Insufficient file permissions! ' + req.args.path);
                } else if (reason === "domainPolicy") {
                    failWithThrow('App cannot be used from this domain! ' + req.args.path);
                }
            } break;

            // 404: File not found: {fileId}
            //      --- Should never happen in production, this means there's a bug in our code.
            case 404: {
                failWithThrow('Bad request! ' + req.args.path);
            } break;

            // 500: Backend Error
            //      --- Exponential backoff
            case 500: {
                console.debug('handleError - reqIdx: ' + requestIdx + '(' + req.args.path +') - [code: ' + syncError.code + ', message: ' + syncError.message + ', reason: ' + syncError.reason +']. Retrying #attempt: ' + req.attempt);
                tryReatempt();
            } break;
        }
    }

    // Figure out when to use response and when and how to use rawResponse.
    handleGoogleResponse(requestIdx, response, rawResponse) {
        var req = this.pendingRequests[requestIdx];
        req.resolve(req.okPreprocess(response, rawResponse));
        delete this.pendingRequests[requestIdx];
        this.SyncFS.solveStatusReason("gdrive_offline");
    }

    startRequest(requestArgs, okPreprocess, errorHandler) {
        var _this = this;
        var idx = _this.reqIdx++;
        okPreprocess = okPreprocess ? okPreprocess : function(response, rawResponse) { return response; }; // by default return response unmodified
        errorHandler = errorHandler ? errorHandler : function(error) { return false; }; // don't handle any errors by default
        var rArgs = requestArgs;

        return new Promise((resolve, reject) => {
            rArgs.callback = function(response, rawResponse) {
                if (typeof rawResponse === 'string') {
                    rawResponse = JSON.parse(rawResponse);
                }
                var rawResponseSuccess = (rawResponse !== undefined && rawResponse.hasOwnProperty('gapiRequest') && rawResponse.gapiRequest.hasOwnProperty('data') && rawResponse.gapiRequest.data.hasOwnProperty('status') && rawResponse.gapiRequest.data.status === 200) || false;
                var responseHasFiles = (response && response.hasOwnProperty('files') && Array.isArray(response.files)) || false;
                var responseHasChanges = (response && response.hasOwnProperty('changes') && Array.isArray(response.changes)) || false;
                if (rawResponseSuccess
                        || responseHasFiles
                        || responseHasChanges
                        || (
                                (!response || response === null)
                                || (!response.error || response.error === null)
                                || errorHandler(response.error)
                        )
                ) {
                    _this.handleGoogleResponse(idx, response, rawResponse);
                } else {
                    if (response && response.error) {
                        var e = response.error;
                        _this.handleGoogleError(idx, new SyncError(e.message, e.code, e.errors[0].reason));  // TODO(Catalin): Is this reliable? Documentation doesn't explain why an error contains a list of errors, but a single message and a single error code...
                    } else {
                        console.warn('Non successful response ' + idx + ' has no error property: ', response, 'rawResponse: ', rawResponse);
                    }
                }
            };

            rArgs.errorCallback = function(errorMessage) {
                if (errorMessage !== "timeout")
                {
                    console.error("Failed to execute google request:", chrome.runtime.lastError);
                    reject(new SyncError(chrome.runtime.lastError));
                } else {
                    // If we get a 5 sec timeout, assume google is down, and start pinging.
                    reject(new SyncError("timeout", 500));
                }
                _this.attemptGoogleReconnect();
            };

            _this.pendingRequests[idx] = {
                args: rArgs,
                attempt: 0,
                okPreprocess: okPreprocess,
                resolve: resolve,
                reject: reject,
            };

            this.enqueueRequest(rArgs);
        });

    }

    enqueueRequest(args) {
        this.requestQueue.push(args);
    }

    startPerformingRequests() {
        var _this = this;
        if (_this.intervalID !== undefined) return;
        _this.intervalID = window.setInterval(() => { _this.performNextRequest(); }, _this.requestInterval);
    }

    stopPerformingRequests() {
        if (this.intervalID === undefined) return;
        window.clearInterval(this.intervalID);
        this.intervalID = undefined;
    }

    performNextRequest() {
        var _this = this;
        if (this.requestQueue.length > 0) {
            var requestArgs = this.requestQueue.shift();
            gapi.client.request(requestArgs);
        }
    }

    // Query format: https://developers.google.com/drive/v3/web/search-parameters, use empty string for all files/directories
    getFileList = (query) => {
        return new Promise((resolve, reject) => {
            var _this = this;
            var files = [];
            var fetchNextPage = function(pageToken) {
                _this._fetchFileListPage(query, pageToken, function(response) {
                    files = files.concat(response.files);
                    if ("nextPageToken" in response) {
                        fetchNextPage(response.nextPageToken);
                    } else {
                        resolve(files);
                    }
                }, function(error) {
                    reject(error);
                });
            };
            // fetch the first page
            fetchNextPage(null);
        });
    };

    downloadFile = (fileId, name) => {
        var responseType = 'arraybuffer';
        if (name && typeof name === 'string') {
            if (name.lastIndexOf('.') !== -1) {
                var extension = name.substr(name.lastIndexOf('.')).toLowerCase();
                if (extension === '.txt' || extension === '.json' || extension === '.proj') {
                    responseType = 'text';
                }
            }
        }

        var args = {
            path: "/drive/v3/files/" + fileId,
            params: {
                alt: "media"
            },
            responseType: responseType,
        };

        if (window.ENABLE_DEBUG) {
            console.debug('Starting download of ' + name + '('+fileId+') using xhr responseType: ' + responseType);
        }
        if (responseType === 'arraybuffer') {
            return this.startRequest(args, (response, rawResponse) => {
                    if ('ArrayBuffer' === response.constructor.name) {
                        var responseArr = new Uint8Array(response);

                        try {
                            let base64 = StringView.bytesToBase64(responseArr); // Make b64 string from typed array
                            if (!this._isBase64(base64)) {
                                return response;
                            }
                            console.log('downloadFile :: got base64 ArrayBuffer response payload for file "' + args.path + '" - decoding the response');
                            return this._base64Decode(base64);
                        } catch (e) {
                            return response;
                        }
                    } else {
                        console.log('downloadFile :: got ' + response.constructor.name + ' response payload for file "' + args.path + '"');
                        if (this._isBase64(response)) {
                            console.log('downloadFile :: got base64 encoded ' + response.constructor.name + ' response payload for file "' + args.path + '" - decoding the response');
                            const decodedResponse = this._base64Decode(response);
                            return decodedResponse;
                        }
                    }
                return response;
            });
        } else {
            return this.startRequest(args, this._base64Decode);
        }

    };

    uploadFile = (fileId, name, data, timestamp) => {
        var method = fileId ? "PATCH" : "POST";
        var path = "/upload/drive/v3/files" + (fileId ? "/" + fileId : "");

        var metadata = {
            "name": name,
            "modifiedTime": timestamp
        };

        if (!fileId) {
            metadata.parents = ["appDataFolder"];
            metadata.createdTime = timestamp;
        }

        var boundary = "#g4RJCrhHMbzaPtxpNSAw#";
        var dataStr = this._base64Encode(new Uint8Array(data));
        var body = "";
        body += "--" + boundary + "\n";
        body += "Content-Type: application/json; charset=UTF-8\n\n";
        body += JSON.stringify(metadata) + "\n\n";
        body += "--" + boundary + "\n";
        body += "Content-Type: application/octet-stream\n\n";
        body += dataStr + "\n";
        body += "--" + boundary + "--";

        return this.startRequest({
            path: path,
            params: {
                uploadType: "multipart",
                fields: "id,name,modifiedTime"
            },
            method: method,
            bodyType: "multipart/related; boundary=" + boundary,
            body: body,
        });
    };

    /**
     * Implementation that can upload a file using resumable method
     * https://developers.google.com/drive/v3/web/resumable-upload
     *
     * The steps are:
     *  1) Initiate the resumable upload session
     *  2) Receive the response and extract the Location header that contains the resumable session uri
     *  3) Upload the file to the resumable session uri. This can either be done as 'single request' or as 'multiple chunks'
     *  3a) Single request:
     *      - Create a PUT request to the resumable session uri
     *      - Add the file data to the request body
     *      - Add a Content-Length header, with the number of bytes in the file (body)
     *      - Send the request
     *  3b) Multiple Chunks:
     *      1) Create a PUT request to the resumable session uri
     *      2) Add chunk data to request body. Create chunks in multiples of 256KB (256*1024 bytes) in size, except for the final chunk that completes the upload.
     *          (Keep the chunk size as large as possible so that the upload is efficient)
     *      3) Add two headers:
     *          - Content-Length: Set to the number of bytes in the current chunk
     *          - Content-Range: Set to show which bytes in the file you are uploading.
     *            For example, Content-Range: bytes 0-524287/2000000 shows that you are uploading the first 524,288 bytes (256 x 1024 x 2) in a 2,000,000 byte file.
     *      4) Send the request, and process the response
     *      5) Repeat steps 1-4 for each remaining chunk in the file.
     *         Use the 'Range' header in the response to determine where to start the next chunk.
     *         Do NOT assume that the server received all bytes sent in the previous request
     *
     * @param fileId
     * @param name
     * @param data
     * @param timestamp
     * @return {*}
     */
    uploadFileResumable = (fileId, name, data, timestamp) => {
        var initMethod = fileId ? 'PATCH' : 'POST';
        var path = "/upload/drive/v3/files" + (fileId ? "/" + fileId : "");
        console.log(`${initMethod} ${data.byteLength} bytes to ${path}`);

        var metadata = {
            "name": name,
            "modifiedTime": timestamp,
            "type": 'arraybuffer'
        };

        if (!fileId) {
            metadata.parents = ["appDataFolder"];
            metadata.createdTime = timestamp;
        }

        var body = JSON.stringify(metadata);

        var initResumableParams = {
            'path': path,
            'method': initMethod,
            'params': {
                'uploadType': 'resumable',
                'fields': 'id,name,modifiedTime'
            },
            'headers': {
                'Content-Type': 'application/json; charset=UTF-8',
                'X-Upload-Content-Type' : 'application/octet-stream',
                'X-Body-Content-Length' : body.length,
                'X-Upload-Content-Length' : data.byteLength,
            },
            'bodyType': 'application/json; charset=UTF-8',
            'body': body,
        };

        var processInitResumableResponseHandler = function(response, rawResponse) {
            var responseHeaders = rawResponse && rawResponse.gapiRequest && rawResponse.gapiRequest.data ? rawResponse.gapiRequest.data.responseHeaders : null;
            if (responseHeaders !== null) {
                if (responseHeaders.hasOwnProperty('location')) {
                    return responseHeaders['location'];
                } else if (responseHeaders.hasOwnProperty('Location')) {
                    return responseHeaders['Location'];
                }
            }
            console.error('uploadFileResumable :: Failed Initializing resumable upload and getting session url. InitResumableParams: ', initResumableParams ,' RawResponse', rawResponse);
            throw 'Failed initializing uploadResumable for file ' + name + ' (fileId: ' + fileId + ')';
        };

        return this.startRequest(initResumableParams, processInitResumableResponseHandler)
                .then((resumableSessionUri) => {

                    var chunkSize = window.GD_RESUMABLE_UPLOAD_CHUNK_SIZE;

                    /**
                     * Calculates the number of chunks still needed before completing the upload.
                     * We cannot rely on the values we use in the Content-Range header of the request, instead we use the Range header in the response to get the number of bytes that was actually received by the server.
                     *
                     * @param range
                     * @return {Number} The number of chunk requests needed (at least) before the upload can be considered to be complete.
                     * @private
                     */
                    var _getRemainingChunkCount = (range) => {
                        var chunkCount = parseInt(((data.byteLength - range) / chunkSize).toFixed(0));
                        if (((data.byteLength - range) / chunkCount) > chunkSize) {
                            chunkCount += 1;
                        }
                        return chunkCount;
                    };

                    /**
                     * This function can prepare parameters for the multiple-chunk resumable uploads to google drive
                     * @param startByte - The offset from where to start the range selection of the file bytes
                     * @return {*} Returns an object that can be used as 1st argument for startRequest
                     * @private
                     */
                    var _prepareParams = (startByte) => {
                        var endByte = Math.min(startByte + chunkSize - 1, data.byteLength -1);
                        var remainingChunkCount = _getRemainingChunkCount(startByte);
                        // console.log(`uploadFileResumable :: prepareParams: ${startByte}-${endByte}/${data.byteLength} (chunkSize: ${chunkSize}, remainingChunkCount: ${remainingChunkCount}) - (${fileId}-${name})`);
                        if (startByte >= (data.byteLength-1) || startByte >= endByte) {
                            // Signal error by returning null
                            return null;
                        }

                        var requestParams = {
                            'path' : resumableSessionUri.substr('https://www.googleapis.com'.length),
                            'method' : 'PUT', /* PATCH? */
                            'headers' : {
                                'Content-Range': `bytes ${startByte}-${endByte}/${data.byteLength}`,
                                'X-Chunk' : `${remainingChunkCount} chunks remaining of size ${chunkSize} bytes`,
                            },
                            'bodyType': initResumableParams['headers']['X-Upload-Content-Type'],
                            'body' : new Uint8Array(data).subarray(startByte, endByte + 1)
                        };
                        return requestParams;

                    };

                    /**
                     * This is a custom okPreprocess handler that can be used as 2nd argument to startRequest for the chunk upload requests.
                     *
                     * The value returned by this handler can be used as the next 'start offset' for the subsequent Content-Range header, or to consider the upload complete, or to consider it failed.
                     *
                     * @param response
                     * @param rawResponse
                     * @return {number|string|null} If status of the rawResponse is 308 and the Range header is present, the "endbyte" + 1 is returned (range: bytes 512-2048 ==> 2049 would be returned).
                     * If the status is 200 or 201 - the string 'resumable_upload_complete' is returned, and if the status is neither 200/201 and no Range header is present - null is returned.
                     * @private
                     */
                    var _chunkResponseHandler = function(response, rawResponse) {
                        if (typeof rawResponse === 'string') {
                            rawResponse = JSON.parse(rawResponse);
                        }
                        var responseHeaders = rawResponse && rawResponse.gapiRequest && rawResponse.gapiRequest.data ? rawResponse.gapiRequest.data.responseHeaders : null;
                        if (responseHeaders) {
                            var rangeHeaderValue;
                            if (responseHeaders.hasOwnProperty('range')) {
                                rangeHeaderValue = responseHeaders['range'];
                            } else if (responseHeaders.hasOwnProperty('Range')) {
                                rangeHeaderValue = responseHeaders['Range'];
                            }
                            if (rangeHeaderValue) {
                                //Expected Range header value: 'bytes=0-524287'
                                if (rangeHeaderValue.startsWith('bytes=')) {
                                    var byteParts = rangeHeaderValue.substr('bytes='.length).split('-');
                                    if (byteParts.length === 2) {
                                        var startByte=byteParts[0];
                                        var endByte = byteParts[1];
                                        return parseInt(endByte) + 1;
                                    } else {
                                        console.warn('Unsupported "Range" header value: ' + rangeHeaderValue);
                                    }
                                }
                            } else if (responseHeaders.status === 200 || responseHeaders.status === 201) {
                                // Upload complete
                                return 'resumable_upload_complete';
                            } else if (responseHeaders.status === 404) {
                                console.log('uploadFileResumable :: _chunkResponseHandler :: Got a "not found" response');
                            } else if (responseHeaders.status === 403) {
                                console.log('uploadFileResumable :: _chunkResponseHandler :: Got a "403" response', rawResponse);
                                return null;//'resumable_upload_timeout'
                            } else if (responseHeaders.status === 500) {
                                console.log('uploadFileResumable :: _chunkResponseHandler :: Got a "' + responseHeaders.status + '" response', rawResponse);
                                return null;//'resumable_upload_timeout';
                            } else {
                                console.log('uploadFileResumable :: _chunkResponseHandler :: No "Range" header. Response status: ' + responseHeaders.status, responseHeaders);
                            }
                        }
                        console.warn('uploadFileResumable :: _chunkResponseHandler - Not a valid rawResponse', rawResponse);
                        return null;
                    };

                    var _this = this;
                    var _promiseHandler = function(range) {
                        if (range === null) {
                            return Promise.reject('Range was null');
                        }
                        if (range === 'resumable_upload_complete') {
                            return Promise.resolve(range);
                        }

                        var remainingChunkCount = _getRemainingChunkCount(range);
                        if (remainingChunkCount >= 0) {
                            var nextRequestParams = _prepareParams(range);
                            if (nextRequestParams && nextRequestParams !== null) {
                                console.debug('uploadFileResumable :: Continuing resumable upload - [remainingChunks: ' + remainingChunkCount + ' - Content-Range: ' + nextRequestParams['headers']['Content-Range'] + ', chunkSize: ' + chunkSize+']');
                                return _this.startRequest(nextRequestParams, _chunkResponseHandler) //TODO: Add errorResponseHandler (and implement retry logic)
                                        .then(_promiseHandler);
                            }
                        } else {
                            // console.log('uploadFileResumable :: resolving _promiseHandler - remainingChunkCount: ' + remainingChunkCount);
                            return Promise.resolve('resumable_upload_complete');
                        }
                    };

                    var firstRangeParams = _prepareParams(0);
                    return this.startRequest(firstRangeParams, _chunkResponseHandler)
                            .then(_promiseHandler)
                            .then((result) => {
                                console.log('uploadFileResumable :: Resumable upload of '+ name +' (id: '+ fileId +') done!', result);
                                return Promise.resolve();
                            });
                });

    };

    moveFile = (fileId, newPath, timestamp) => {
        return this.startRequest({
            'path': '/drive/v3/files/' + fileId,
            'params': {
                fields: "id,name,modifiedTime"
            },
            'method': "PATCH",
            body: {
                name: newPath,
                modifiedTime: timestamp
            },
        });
    };

    copyFile = (fileId, newPath, timestamp) => {
        return this.startRequest({
            path: "/drive/v3/files/" + fileId + "/copy",
            params: {
                fields: "id,name,modifiedTime"
            },
            method: "POST",
            body: {
                name: newPath,
                parents: ["appDataFolder"],
                modifiedTime: timestamp
            },
        });
    };

    deleteFile = (fileId, ignoreFileNotFound) => {
        return this.startRequest({
            path: "/drive/v3/files/" + fileId,
            method: "DELETE",
            callback: function(metadata, rawResponse) {
                return metadata;
            },
            errorCallback: function(metadata, rawResponse) {
                return rawResponse;
            }
        }, undefined, function(error) {
            // Don't treat 404: File Not Found as errors when deleting files -
            // sometimes we cannot really tell whether a file would exist on Google Drive
            // and it doesn't make sense to execute an additional request to check that.
            return ignoreFileNotFound && error.code === 404;
        });
    };

    getChangeStartPageToken = () => {
        return this.startRequest({
            path: "/drive/v3/changes/startPageToken",
            callback: function(response, rawResponse) {
                return response.startPageToken;
            },
            errorCallback: function(response, rawResponse) {
                return rawResponse;
            }
        });
    };

    getChangeList = (startPageToken) => {
        return new Promise((resolve, reject) => {
            var _this = this;
            var changes = [];
            var fetchNextPage = function(pageToken) {
                _this._fetchChangesPage(pageToken, function(response) {
                    changes = changes.concat(response.changes);
                    if ("nextPageToken" in response) {
                        fetchNextPage(response.nextPageToken);
                    } else {
                        resolve({
                            changes: changes,
                            newStartPageToken: response.newStartPageToken
                        });
                    }
                }, function(error) {
                    reject(error);
                });
            };
            // fetch the first page
            fetchNextPage(startPageToken);
        });
    };

    _fetchFileListPage = (query, pageToken, okCallback, errorCallback) => {
        console.log("fetching page with token " + pageToken);
        var params = {
            "q": query,
            "pageSize": 1000,
            "spaces": "appDataFolder",
            "fields": "nextPageToken,files(id,name,modifiedTime)"
        };
        if (pageToken) {
            params.pageToken = pageToken;
        }

        this.startRequest({
            path: "/drive/v3/files",
            params: params,
        })
        .then(okCallback)
        .catch(errorCallback);
    };

    _fetchChangesPage = (pageToken, okCallback, errorCallback) => {
        var params = {
            "pageToken": pageToken,
            "includeRemoved": true,
            "pageSize": 1000,
            "restrictToMyDrive": false,
            "spaces": "appDataFolder"
        };

        this.startRequest({
            path: "/drive/v3/changes",
            params: params,
        })
        .then(okCallback)
        .catch(errorCallback);
    };

    _base64Encode = (bytes) => {
        if (!this._hasByteLengthProperty(bytes)) {
            if (this._isBase64(bytes)) {
                //Input is already base64 encoded
                return bytes;
            }
            console.warn('Cannot encode input - it does not have a byteLength property which is required');
            return bytes;
        }
        var binary = '';
        var len = bytes.byteLength;
        for (var i = 0; i < len; i++) {
            binary += String.fromCharCode(bytes[i]);
        }
        return window.btoa(binary);
    };

    _base64Decode = (encodedDataStr) => {
        if (!this._isBase64(encodedDataStr) && this._hasByteLengthProperty(encodedDataStr)) {
            // input is not base64, and has a byteLength property - skip decoding
            return encodedDataStr;
        }
        var binary = window.atob(encodedDataStr);
        if (this._isBase64(binary)) {
            /// Workaround for bad sync behavior - Some files have been uploaded as b64 that was encoded more than once... in those cases we will have to decode more than once to get the original binary :-/
            return this._base64Decode(binary);
        }
        var buffer = new ArrayBuffer(binary.length);
        var view = new Uint8Array(buffer);
        for (var i = 0; i < binary.length; i++) {
            view[i] = binary.charCodeAt(i);
        }
        return buffer;
    };

    _hasByteLengthProperty = (input) => {
        if (input && input !== null) {
            return (typeof input.byteLength === 'number');
        }
        return false;
    };

    _isBase64 = (input) => {
        if (!input || (typeof input !== 'string')) {
            return false;
        }

        const base64CharRegex = /^([0-9a-zA-Z+/]{4})*(([0-9a-zA-Z+/]{2}==)|([0-9a-zA-Z+/]{3}=))?$/;
        /*
            ^                          # Start of input
            ([0-9a-zA-Z+/]{4})*        # Groups of 4 valid characters decode
                                       # to 24 bits of data for each group
            (                          # Either ending with:
                ([0-9a-zA-Z+/]{2}==)   # two valid characters followed by ==
                |                      # , or
                ([0-9a-zA-Z+/]{3}=)    # three valid characters followed by =
            )?                         # , or nothing
            $                          # End of input
         */
        return base64CharRegex.test(input);
    };

}



